import React, { Component } from 'react';
import { Icon, Button, message } from 'antd';
import axios from 'axios';
import ImagePicker from 'antd-mobile/lib/image-picker';
import { Link } from 'react-router-dom';
import ActivityIndicator from 'antd-mobile/lib/activity-indicator';
import WingBlank from 'antd-mobile/lib/wing-blank';

import './goodPingjia.css';
import '../../../../component/apis';

const data = [];

//
message.config({
  top: 300,
});

class AddPingJia extends Component {
  constructor(props) {
    super(props);
    this.state = {
      animating: false,
      files: data,
    }
    // console.log(props);
  }

  UNSAFE_componentWillMount() {
    axios.post(global.constants.website+'/api/task/additionalTask', {
      order_id: localStorage.getItem("order_id"),   //order_id
    },{
      headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
    })
    .then( res=> {
      let datas = res.data.data;
      // console.log(datas);
      this.setState({
        order_id: datas.order_id,                   //order_id
        template_pic: datas.template_pic,           //
        additional_pic: datas.additional_pic,       //
      })
    })
    .catch(error => {
      console.log(error);
    })
  }

  //  
  onChange = (files, type, index) => {
    // console.log(files);
    this.setState({
      files,
    });
  }
  //  ajax
  uploadShouHuo = () => {
    let photos = this.state.files;        //
    let this_ = this;
    if ( photos.length === 1 ) {
      this_.setState({ animating: true })            //login.....
      let imgs = [photos[0].url];    //
      axios.post(global.constants.website+'/api/task/additioncommit',{
        order_id: localStorage.getItem("order_id"),   //order_id,           //ID
        addition_pic: imgs,             //
      },{
        headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
      })
      .then( res => {
        // console.log(res.data.status);
        if ( res.data.status ) {
          this_.setState({ animating: false })          //login.....
          this_.props.history.push("/myTaskDetails");
          message.success(res.data.msg);
        } else {
          this_.setState({ animating: false })          //login.....
          message.error(res.data.msg)
        }
      })
      .catch( error => {
        console.log(error);
      })
    }else {
      //ajax
      if ( photos.length > 1 ) {
        message.error('1');
      } else {
        message.error('');
      }
    }

  }

  render() {
    const { additional_pic,template_pic,files,animating } = this.state;
    return(
      <div>
        <header className="tabTitle">
          <div className="return"><Link to="/myTaskDetails"><Icon type="left" theme="outlined" /></Link></div>
          
        </header>
        <div>
          <WingBlank>
            <div className="goodPingJia-box">
              <h2>  !</h2>
              <p></p>
              <div className="goodPingJia-box-child">
                <p></p>
                <p>
                  {
                    template_pic ?
                      template_pic.map((item, index) => {
                        return(
                          <img key={index} style={{ maxWidth: '100%' }} src={item} alt=""/>
                        )
                      })
                    :
                    ""
                  }
                </p>
                <p></p>
                <p>
                  {
                    additional_pic ?
                      additional_pic.map((item, index) => {
                        return(
                          <img key={index} style={{ maxWidth: '100%', paddingTop: '0.5rem' }} src={item} alt=""/>
                        )
                      })
                    :
                    ""
                  }
                </p>
              </div>
              <p> </p>
              <ImagePicker
                length={1}
                files={files}
                multiple={false}
                onChange={this.onChange}
                onImageClick={(index, fs) => console.log(index, fs)}
                selectable={files.length < 1}
              />
              <p><span style={{ fontWeight:'bold',fontSize:'1rem',color:'red' }}></span></p>
            </div>
            <Button type="primary" className="login-form-button" onClick={this.uploadShouHuo}>
              
            </Button>
            <div className="toast-example">
              <ActivityIndicator
                toast
                text="..."
                animating={animating}
              />
            </div>
          </WingBlank>
        </div>
      </div>
    )
  }
}

export default AddPingJia
