import React, { Component } from 'react';
import { Form, Input, Button, Checkbox, message } from 'antd';
import { Link } from 'react-router-dom';
import axios from 'axios';
import ActivityIndicator from 'antd-mobile/lib/activity-indicator';
import WingBlank from 'antd-mobile/lib/wing-blank';
import './login.css';
import '../../component/apis';
// import '../../component/mui';
// console.log(window.mui);
// global.constants.website+
// ,"proxy": "https://budan.php12.cn/"          

const FormItem = Form.Item;
const phoneNum = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/;   //
// window.location.reload();

class Logins extends Component {
  constructor(props) {
    super(props);
    this.state = {
      stateID1: true,
      stateID2: true,
      ticket: '',
      randstr: '',
      animating: false,
    }
  }

  componentDidMount() {
    // localStorage.setItem("numA", 1);
    if (localStorage.getItem("numAs") === "2") {//token
      localStorage.removeItem("numAs");
      window.location.reload();
    }
    let this_ = this;
    window.callback = function (res) {
      if ( res.ret === 0 ) {
        //  ajax
        axios.post(global.constants.website+'/api/user/login', {
          // 
          mobile: this_.state.mobile,
          password: this_.state.password,
          ticket: res.ticket,
          randstr: res.randstr,
        })
        .then(function (response) {   //
          // console.log(response);
          //  token
          if ( response.data.status ) {
            if ( this_.state.remember ) {                                      //
              localStorage.setItem("mobile", this_.state.mobile);            //
              localStorage.setItem("password", this_.state.password);          //
            } else {                                                      //
              localStorage.removeItem("mobile");                          //
              localStorage.removeItem("password");
            }
            // message.success(response.data.msg)
            this_.setState({ animating: true })            //login.....
            //   // token
              localStorage.setItem("token", response.data.token);
              localStorage.setItem("numAs", "2");
              //   // 
              axios.post(global.constants.website+'/api/index/device',
                {
                  imei: localStorage.getItem("uuid"),                                 //
                  latitude: localStorage.getItem("latitude"),                         //
                  altitude: localStorage.getItem("longitude"),                      //
                  is_wifi: localStorage.getItem("isWifi") === "true" ? 1 : 0,        //4Gwifi
                  smcard_name: localStorage.getItem("imsi"),                       //SM
                  platform: localStorage.getItem("platform"),                       //12IOS
                },
                {
                  headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
                })
                .then(res => {//data.
                  if ( res.data.status ) {
                    console.log(res.data.status);
                    this_.setState({ animating: false })          //login.....
                    message.success(response.data.msg);
                    // 
                    this_.props.history.push({pathname: '/taskHallPage', state: {token: response.data.token}});
                  }else {
                    this_.setState({ animating: false })          //login.....
                    message.error(res.data.msg);
                    // message.error("");
                  }
                })
                .catch(err => {
                  console.log(err);
                })

            // 
            // message.success(response.data.msg, successSkip => {
            //   // token
            //   localStorage.setItem("token", response.data.token);
            //   localStorage.setItem("numAs", "2");
            //   // 
            //   axios.post(global.constants.website+'/api/index/device',
            //     {
            //       imei: localStorage.getItem("uuid"),             //
            //       latitude: localStorage.getItem("latitude"),     //
            //       altitude: localStorage.getItem("longitude"),    //
            //       is_wifi: localStorage.getItem("isWifi"),          //4Gwifi
            //       smcard_name: localStorage.getItem("imsi"),      //SM
            //     },
            //     {
            //       headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
            //     })
            //     .then(res => {
            //       console.log(res);
            //       this_.setState({ animating: false })          //login.....
            //     })
            //     .catch(err => {
            //       console.log(err);
            //     })
            //
            //   this_.props.history.push({pathname: '/taskHallPage', state: {token: response.data.token}});
            // })

          } else {  // response.data false
            message.error(response.data.msg)
          }
        })
        .catch(function (error) {   //
          console.log(error);
        });
      }
    }
    this.props.form.setFieldsValue({
    userName: localStorage.getItem("mobile"),                //
    password: localStorage.getItem("password"),              //
  })
  if ( localStorage.getItem("mobile") || localStorage.getItem("password") ) {
    this.setState({
      stateID1: false,
      stateID2: false,
    })
  }
  }


  handlePhone = (e) => {
    this.setState({
      stateID1: false,
    })
  }
  handlePwd = (e) => {
    this.setState({
      stateID2: false,
    })
  }

  // 
  handleSubmit = (e) => {
    let this_ = this;
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        // console.log(values);
        if ( !phoneNum.test(values.userName) ) {    //
          message.error("")
        } else {
          this_.setState({
            mobile: values.userName,
            password: values.password,
            remember: values.remember,
          })
          // //  ajax
          // axios.post(global.constants.website+'/api/user/login', {
          //   // 
          //   mobile: values.userName,
          //   password: values.password,
          // })
          // .then(function (response) {   //
          //   // console.log(response);
          //   //  token
          //   if ( response.data.status ) {
          //     if ( values.remember ) {                                      //
          //       localStorage.setItem("mobile", values.userName);            //
          //       localStorage.setItem("password", values.password);          //
          //     } else {                                                      //
          //       localStorage.removeItem("mobile");                          //
          //       localStorage.removeItem("password");
          //     }
          //     // message.success(response.data.msg)
          //     message.success(response.data.msg, successSkip => {
          //       // token
          //       localStorage.setItem("token", response.data.token);
          //       this_.props.history.push({pathname: '/taskHallPage', state: {token: response.data.token}});
          //     })
          //   } else {  // response.data false
          //     message.error(response.data.msg)
          //   }
          // })
          // .catch(function (error) {   //
          //   console.log(error);
          // });
        }
      }
    });
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    const { stateID1, stateID2, animating } = this.state;
    return(
      <div>
        <WingBlank style={{ margin: '0 auto' }}>
          <img style={{ width: "100%", display: 'block', marginBottom: '0.5rem' }} src={ require("../../img/1-wap.jpg") } alt="login"/>
          <Form onSubmit={this.handleSubmit} className="login-form" style={{width: "90%"}}>
            <FormItem label="">
              {getFieldDecorator('userName', {
                rules: [{ required: true, message: '!' }],
              })(
                <Input onChange={this.handlePhone} style={{ width: '100%', height: '41px', margin: '0' }} type="text" maxLength={11} placeholder="" />
              )}
            </FormItem>
            <FormItem label="">
              {getFieldDecorator('password', {
                rules: [{ required: true, message: '!' }],
              })(
                <Input onChange={this.handlePwd} style={{ width: '100%', height: '41px', margin: '0' }} type="password" placeholder="" />
              )}
            </FormItem>
            <FormItem style={{ width: "90%", margin: "0 auto" }}>
              {getFieldDecorator('remember', {
                valuePropName: 'checked',
                initialValue: true,
              })(
                <Checkbox></Checkbox>
              )}
              {/* <a href=""></a> disabled={stateID?true: false}  */ }
              <Link className="login-form-forgot" to="/forgetPassword"></Link>
              <Button disabled={stateID1||stateID2?true:false} type="primary" htmlType="submit" className="login-form-buttons" id="TencentCaptcha" data-appid="2020178866" data-cbfn="callback"></Button>
              <Button className="login-form-buttons"><Link to="/registerPage"></Link></Button>
            </FormItem>

            <div className="toast-example">
              <ActivityIndicator
                toast
                text="..."
                animating={animating}
              />
            </div>
          </Form>
        </WingBlank>
      </div>
    )
  }
}

const Login = Form.create()(Logins);
export default Login
