import React, { Component } from 'react';
import { Icon, Input, Button, message, Modal, Form } from 'antd';
import { Link } from 'react-router-dom';
import axios from 'axios';
import WhiteSpace from 'antd-mobile/lib/white-space';

import './cash.css';
import '../../component/apis';

class CashPage extends Component {
  constructor() {
    super();
    this.state = {
      inputYJ: "",                      //input
      inputBJ: "",                      //input
      visibleBJ: false,                   //modal
      getCodesState: true,                //true  
      codeNum: 60,                        //  60s
      tuValue: null,                      //value
      phoneCodes: null,                   //input 
      TestGetCode: "",           //
    }
  }

  UNSAFE_componentWillMount() {
    axios.get(global.constants.website+'/api/index/cash',
    {
      headers: {AppAuthorization: localStorage.getItem("token")}
    })
    .then( res => {
      // console.log(res.data.data);
      let datas = res.data.data;
      this.setState({
        cashBJ: datas.money_account,                      //
        cashYJ: datas.commission_account,                 //
        mobile: datas.mobile,                             //
        bank_name: datas.bank_name,                       //
        bank_card_NO: datas.bank_card_NO,                 //
        bank_status: datas.bank_status,                   // 0 1 2  3
        realname_status:datas.realname_status,            // 0 1 2  3
        is_black: datas.is_black,                         //1
        withdraw_account: datas.withdraw_account,         //
      });
    })
    .catch(error => {
      console.log(error);
    });

    // 
    axios.get(global.constants.website+'/api/user/getcaptcha')
    .then(response => {
      this.setState({
        tuCodeLink: response.data.data.captcha_src,
        sid: response.data.data.sid,
      })
      // console.log(response.data.data);
    })
    .catch(error => {
      console.log(error);
    })
  }
  componentWillUnmount = () => {
    this.setState = (state,callback)=>{
      return;
    };
  }

  //  
  getVerifyCode = () => {
    axios.get(global.constants.website+'/api/user/getcaptcha')
    .then(response => {
      this.setState({
        tuCodeLink: response.data.data.captcha_src,
        sid: response.data.data.sid,
      })
    })
    .catch(error => {
      console.log(error);
    })
    // console.log(this.state.tuCode);
  }

  // value
  tuCode = (e) => {
    this.setState({
      tuValue: e.target.value
    })
  }
  // value
  phoneCode = (e) => {
    this.setState({
      phoneCodes: e.target.value
    })
  }

  // 
  setCode = () => {
    let states = this.state;
    let this_ = this;   //  this
    if (states.tuValue === null) {
      message.error("");
    } else {
      // 
      axios.post(global.constants.website+'/api/user/sendcode', {
        sid: states.sid,
        tuCode: states.tuValue,
        phoneNum: states.mobile
      })
      .then(function (response) {   //
        // console.log(response.data);
        //  status  true  
        if ( response.data.status ) {
          //  
          let codeNum = states.codeNum;
          const timer = setInterval(() => {
          this_.setState({
            getCodesState:false,
            codeNum: (codeNum--)
            }, () => {
                if (codeNum === 0) {
                clearInterval(timer);
                this_.setState({
                  getCodesState: true,
                  codeNum: 60,
                  TestGetCode: ""
                })
              }
            })
          }, 1000)
          // 
          message.success(response.data.msg);
        } else {  //  status  falseelse
          // 
          message.error(response.data.msg);
        }
      })
      .catch(function (error) {   //
        console.log(error);
      });

    }
    // console.log("");
  }

  // 
  bJTiXianBtn = () => {
    let this_ = this;
    let stateData = this.state;
    // console.log(this.state.cashBJ);
    if ( stateData.bank_status === 1 && stateData.realname_status === 1 && stateData.is_black === 0 ) {
      if ( this.state.withdraw_account === "0.00" ) {
        message.error('');
      } else {
        this_.setState({
          visibleBJ: true,
        });
        // message.success('2-24');
      }
    } else if ( stateData.bank_status === 2 ) {
      message.warning('');
    } else if ( stateData.bank_status === 3 ) {
      message.warning('');
      this_.props.history.push("/bank");
    } else if ( stateData.bank_status === 0 ) {
      message.warning('');
      this_.props.history.push("/bank");
    } else if ( stateData.realname_status === 0 ) {
      message.warning('');
      this_.props.history.push("/certification");
    } else if ( stateData.realname_status === 2 ) {
      message.warning('');
    } else if ( stateData.realname_status === 3 ) {
      message.warning('');
      this_.props.history.push("/certification");
    } else if ( stateData.is_black === 1 ) {
      message.warning('');
    }
  }
  //
  BJhandleOk = (e) => {
    let that = this.state;
    if ( that.tuValue === null || that.phoneCodes === null ) {
      message.error("")
    } else {
      axios.post(global.constants.website+'/api/index/cashcommit', {
        money: that.withdraw_account,
        smscode: that.phoneCodes,
      },{
        headers: {AppAuthorization: localStorage.getItem("token")}
      })
      .then( res => {
        // console.log(res.data);
        if ( res.data.status ) {
          this.setState({
            visibleBJ: false,
          });
          message.success(res.data.msg);
          this.props.history.push("/myCenter");
        } else {
          this.setState({
            visibleBJ: false,
          });
          message.error(res.data.msg);
        }
      })
      .catch(function (error) {   //
        console.log(error);
      });
    }
  }

  BJhandleCancel = (e) => {
    this.setState({
      visibleBJ: false,
    });
  }

  render() {
    const { withdraw_account, tuCodeLink, codeNum, TestGetCode,bank_name,bank_card_NO } = this.state;
    return(
      <div>
        <header className="tabTitle">
          <div className="return"><Link to="/myCenter"><Icon type="left" theme="outlined" /></Link></div>
          
          <div className="returns"><Link to="/cashRecord"></Link></div>
        </header>
        <WhiteSpace style={{ paddingTop: '3rem' }} />
        <div className="tongs">
          <Icon style={{ paddingRight: '0.4rem' }} type="property-safety" theme="twoTone" />
          <span> {bank_card_NO} {bank_name}</span>
        </div>
        {/*  */}
        <div className="cash">
          <p>
            <span style={{ color: '#01AAED' }}></span>
            <span> { withdraw_account }</span>
          </p>
          <div className="cash-money">
            <p>
              <span></span>
              <Input value={ withdraw_account } type="Number" disabled={true} onChange={ this.benJin } placeholder="" />
            </p>
            {/* <p>
                <span style={{ color: '#01AAED' }}></span>
            </p> */}
          </div>
          <Button onClick={ this.bJTiXianBtn } style={{ width: '80%', margin: '0 auto', display: 'block', marginBottom: '1rem' }} type="primary"></Button>
          <div className="cash-footer">
            <p>1.</p>
            <p>2.500</p>
            <p></p>
            <p>3.500110002</p>
            <p>4.</p>
            <p>&nbsp;&nbsp;a.12:00(18:00-12:00)</p>
            <p>&nbsp;&nbsp;b.18:00(12:00-18:00)</p>
            <p></p>
            {/* style={{ color: '#000' }} */}
          </div>
        </div>
        <WhiteSpace />

        <Modal
          title=""
          visible={this.state.visibleBJ}
          onOk={this.BJhandleOk}
          onCancel={this.BJhandleCancel}
          okText=""
          cancelText=""
        >
          <Form onSubmit={this.handleSubmit}>
            <p></p>
            <div className="modal-top">
              <Input onChange={ this.tuCode } placeholder="" />
              <img onClick={ this.getVerifyCode } src={tuCodeLink} alt=""/>
            </div>
            <p></p>
            <div className="modal-top">
              <Input onChange={ this.phoneCode } placeholder="" />
              {
                this.state.getCodesState
                  ? <Button style={{ marginTop: 0 }} onClick={this.setCode}>{ TestGetCode }</Button>
                  : <Button style={{ marginTop: 0 }} disabled="disabled">{codeNum}</Button>
              }
            </div>
          </Form>
        </Modal>
      </div>
    )
  }
}

export default CashPage
