import React, { Component } from 'react';

import axios from 'axios';
import './taskHall.css';
import RouteTabComponent from '../../component/routeTab/routeTab';  //tabs
import TaskList from './taskList/tiskList';

class TaskHallPage extends Component {
  // constructor(props) {
  //   super(props);
  //   // token
  //   // console.log(localStorage.getItem("token"));
  // }

  componentDidMount() {
    let appList = localStorage.getItem("appList");//
    let phoneNumArr = localStorage.getItem("phoneNumArr");//

    // 
    if (localStorage.getItem("dateNum") === null) {
      localStorage.setItem("dateNum", 1)        //
      function getNowFormatDate() {
          var date = new Date();
          var seperator1 = "-";
          var year = date.getFullYear();
          var month = date.getMonth() + 1;
          var strDate = date.getDate();
          if (month >= 1 && month <= 9) {
              month = "0" + month;
          }
          if (strDate >= 0 && strDate <= 9) {
              strDate = "0" + strDate;
          }
          var currentdate = year + seperator1 + month + seperator1 + strDate;
          return currentdate;
      }
      localStorage.setItem("getNowFormatDate", getNowFormatDate())
      // console.log(getNowFormatDate());
    }
    // 
    function getNewFormatDate() {
        var date = new Date();
        var seperator1 = "-";
        var year = date.getFullYear();
        var month = date.getMonth() + 1;
        var strDate = date.getDate();
        if (month >= 1 && month <= 9) {
            month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
            strDate = "0" + strDate;
        }
        var currentdate = year + seperator1 + month + seperator1 + strDate;
        return currentdate;
    }

    //--
			var date1 = localStorage.getItem("getNowFormatDate");
			var date2 = getNewFormatDate();

			//197011
			var time1 = Date.parse(date1);
			var time2 = Date.parse(date2);

			//
			var dayCount = (Math.abs(time2 - time1))/1000/60/60/24;
      if (dayCount === 30) {
        localStorage.removeItem("dateNum");     //30
        axios.post(global.constants.website+'/api/index/apps',
          {
            app_info: JSON.parse(appList),             //
            platform: localStorage.getItem("platform"),             //1 2ios
          },
          {
            headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
          })
          .then(res => {
            if (res.status) {
              // console.log(':'+res.data.msg);
            }
          })
          .catch(err => {
            console.log(err);
          })
      }
			console.log(dayCount);


    //  ***
    // localStorage.removeItem("phonesNum");
    if (localStorage.getItem("phonesNum") === null) {
      axios.post(global.constants.website+'/api/index/contact',
        {
          mobile: [phoneNumArr],             //
        },
        {
          headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
        })
        .then(res => {
          if (res.status) {
            localStorage.setItem("phonesNum", 1)        //
            // console.log(""+res.data.msg);
          }
        })
        .catch(err => {
          console.log(err);
        })

      // 
      axios.post(global.constants.website+'/api/index/apps',
        {
          app_info: localStorage.getItem("platform") === "1" ? JSON.parse(appList) : JSON.parse(localStorage.getItem("IOSappLists")),             //
          platform: localStorage.getItem("platform"),             //1 2ios
        },
        {
          headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
        })
        .then(res => {
          if (res.status) {
            // console.log(':'+res.data.msg);
          }
        })
        .catch(err => {
          console.log(err);
        })
    }

    // console.log(localStorage.getItem("appList"));
    // console.log(localStorage.getItem("phoneNumArr"));
  }
  // 
  componentWillUnmount = () => {
    this.setState = (state,callback)=>{
      return;
    };
  }

  // 
  clickTuiJian = () => {
    this.props.history.push("/tuiJian")
  }

  render() {

    return(
      <div>
        <header className="tabTitle" style={{ position: 'inherit' }}></header>
        {/*  toke={ this.props.location.state.token }  */}
        <TaskList history ={this.props.history} />
        {/* tabs */}
        <RouteTabComponent />
        {/* // <img className="getJinagli" onClick={ this.clickTuiJian } src={ require("../../img/download.gif") } alt=""/> */}
      </div>
    )
  }
}

export default TaskHallPage
