import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Icon, Form, Input, Button, Row, Col, message } from 'antd';
import axios from 'axios';
import ActivityIndicator from 'antd-mobile/lib/activity-indicator';
import WingBlank from 'antd-mobile/lib/wing-blank';
import '../../../component/apis';

const FormItem = Form.Item;
let phoneNumber = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/;   //

class ForgetPasswords extends Component {
  constructor() {
    super();
    this.state = {
      animating: false,
      getCodesState: true,
      codeNum: 60,
      TestGetCode: "",
      placeholder: "",
      tuValue: null,
    }
  }

  //  
  UNSAFE_componentWillMount() {
    axios.get(global.constants.website+'/api/user/getcaptcha')
    .then(response => {
      let responses = response.data.data
      this.setState({
        tuCodeLink: responses.captcha_src,
        sid: responses.sid,
      })
      // console.log(response.data.data);
    })
    .catch(error => {
      console.log(error);
    })
  }
  componentWillUnmount = () => {
    this.setState = (state,callback)=>{
      return;
    };
  }

  //  onChange placeholder 
  onChange = (e) => {
    this.setState({
      placeholder: e.target.value
    })
  }

  //  
  getVerifyCode = () => {
    axios.get(global.constants.website+'/api/user/getcaptcha')
    .then(response => {
      this.setState({
        tuCodeLink: response.data.data.captcha_src,
        sid: response.data.data.sid,
      })
    })
    .catch(error => {
      console.log(error);
    })
    // console.log(this.state.tuCode);
  }

  // value
  tuCode = (e) => {
    this.setState({
      tuValue: e.target.value
    })
  }

  // 
  setCode = () => {
    let states = this.state;
    let this_ = this;   //  this
    if ( states.placeholder === "" ) {
      message.error("");
    } else if ( !phoneNumber.test(states.placeholder) ) {
      message.error("");
    } else if (states.tuValue === null) {
      message.error("");
    } else {
      // 
      axios.post(global.constants.website+'/api/user/sendcode', {
        sid: states.sid,
        tuCode: states.tuValue,
        phoneNum: states.placeholder
      })
      .then(function (response) {   //
        console.log(response.data);
        //  status  true  
        if ( response.data.status ) {
          //  
          let codeNum = states.codeNum;
          const timer = setInterval(() => {
          this_.setState({
            getCodesState:false,
            codeNum: (codeNum--)
            }, () => {
                if (codeNum === 0) {
                clearInterval(timer);
                this_.setState({
                  getCodesState: true,
                  codeNum: 60,
                  TestGetCode: ""
                })
              }
            })
          }, 1000)
          // 
          message.success(response.data.msg);
        } else {  //  status  falseelse
          // 
          message.error(response.data.msg);
        }
      })
      .catch(function (error) {   //
        console.log(error);
      });

    }
    // console.log("");
  }
  // login
  newBtn = () => {
    window.location.reload();
  }

  //  
  handleSubmit = (e) => {
    e.preventDefault();
    let this_ = this;
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        // console.log('Received values of form: ', values);
        if( values.loginPassword !== values.aloginPassword ) {
          message.error("")
        }else {
          // ajax
          // console.log(values);
          axios.post(global.constants.website+'/api/user/forgetPwd', {
            mobile: values.phoneNum,
            smscode: values.captcha,
            password: values.loginPassword,
            repassword: values.aloginPassword,
          },{
            headers: {AppAuthorization: localStorage.getItem("token")}
          })
          .then( res => {
            // console.log(res.data);
            if ( res.data.status ) {
              message.success(res.data.msg);
              this_.props.history.push("/");
            } else {
              message.error(res.data.msg);
            }
          })
          .catch(error => {
            console.log(error);
          });
          // message.success("", successSkip => { // 
          //   this.props.history.push('/taskHallPage')
          // })
        }
      }
    });
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    const { animating, tuCodeLink } = this.state;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 },
      },
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0,
        },
        sm: {
          span: 16,
          offset: 8,
        },
      },
    };

    return(
      <div>
        <header className="tabTitle">
          <div onClick={this.newBtn} className="return"><Link to="/"><Icon type="left" theme="outlined" /></Link></div>
          
        </header>
        <WingBlank>
          <Form onSubmit={this.handleSubmit} style={{ padding: "3.7rem 0.6rem 0 0.6rem" }}>
            <FormItem
              {...formItemLayout}
              label=""
            >
              {getFieldDecorator('phoneNum', {
                rules: [{
                  required: true, message: '!',
                }],
              })(
                <Input className="register-input" onChange={ this.onChange } maxLength={11} placeholder={ this.state.placeholder } />
              )}
            </FormItem>
            <FormItem
              {...formItemLayout}
              label=""
            >
              <div>
                <img onClick={ this.getVerifyCode } style={{ width: "100%" }} src={ tuCodeLink } alt=""/>
              </div>
              {getFieldDecorator('tuCode', {
                rules: [{
                  required: true, message: '!',
                }],
              })(
                <Input onChange={ this.tuCode } className="register-input" maxLength={11} placeholder="" />
              )}
            </FormItem>
            <FormItem
              {...formItemLayout}
              label=""
            >
              <Row gutter={8}>
                <Col span={16}>
                  {getFieldDecorator('captcha', {
                    rules: [{ required: true, message: '!' }],
                  })(
                    <Input className="register-input" placeholder="" />
                  )}
                </Col>
                <Col span={8}>
                  {
                    this.state.getCodesState
                      ? <Button style={{ marginTop: '0' }} onClick={this.setCode}>{ this.state.TestGetCode }</Button>
                      : <Button style={{ marginTop: '0' }} disabled="disabled">{this.state.codeNum}</Button>
                  }
                </Col>
              </Row>
            </FormItem>
            <FormItem
              {...formItemLayout}
              label=""
            >
              {getFieldDecorator('loginPassword', {
                rules: [{
                  required: true, message: '!',
                }],
              })(
                <Input className="register-input" placeholder="6-16" />
              )}
            </FormItem>
            <FormItem
              {...formItemLayout}
              label=""  style={{ marginBottom: '4rem' }}
            >
              {getFieldDecorator('aloginPassword', {
                rules: [{
                  required: true, message: '!',
                }],
              })(
                <Input className="register-input" placeholder="6-16" />
              )}
            </FormItem>
            <FormItem {...tailFormItemLayout}>
              <Button className="btn-buy" type="primary" htmlType="submit"></Button>
            </FormItem>
            <div className="toast-example">
              <ActivityIndicator
                toast
                text="..."
                animating={animating}
              />
            </div>
          </Form>
        </WingBlank>
      </div>
    )
  }
}

const ForgetPassword = Form.create()(ForgetPasswords);
export default ForgetPassword
