import React,{ Component } from 'react';
import { Link } from 'react-router-dom';
import { Icon, Input, message, Popover } from 'antd';
import axios from 'axios';
import copy from 'copy-to-clipboard';             //

import './tuijian.css';
import '../../../component/apis';

const text = <span></span>;
const content = (
  <div className="contents">
    <p><Icon type="check-circle" theme="twoTone" /></p>
    <p><Icon type="check-circle" theme="twoTone" />1-3</p>
    <p><Icon type="check-circle" theme="twoTone" />0.3-88</p>
    <p><Icon type="check-circle" theme="twoTone" /></p>
    <p><Icon type="check-circle" theme="twoTone" /><span style={{ color: '#2897ff', fontWeight: 'bold' }}>10%5%</span></p>
  </div>
);

class TuiJian extends Component {
  constructor() {
    super();
    this.state= {
      visible: false,
    }
  }

  UNSAFE_componentWillMount() {
    axios.get(global.constants.website+'/api/index/invite', {
      headers: {AppAuthorization: localStorage.getItem("token")}        //post  token
    })
    .then( res => {
      let datas = res.data.data;
      // console.log(datas);
      this.setState({
        YQcode: datas.code,
        money: datas.total_prize,
        use_num: datas.use_num,
        active_num: datas.active_num,
        second_use_num: datas.second_use_num,
        second_active_num: datas.second_active_num,
        placeholder: datas.share_link,                     //
      })
      // console.log(res.data.data);
    })
    .catch(function(error) {
      console.log(error);
    });
  }

  // APP
  cloneBtn = (e) => {
    copy(this.state.placeholder);        //
    message.success('');
  }

  // 
  copyYQcode = () => {
    copy(this.state.YQcode);             //
    message.success('');
  }

  // 
  hide = () => {
    this.setState({
      visible: false,
    });
  }
  handleVisibleChange = (visible) => {
    this.setState({ visible });
  }

  render() {
    const { second_use_num,second_active_num,active_num, YQcode, money, use_num } = this.state;
    return(
      <div style={{ backgroundColor: '#f24d49' }}>
        <header className="tabTitle">
          <div className="return"><Link to="/myCenter"><Icon type="left" theme="outlined" /></Link></div>
          
        </header>
        <img className="top-img" src={ require("../../../img/headerImg.png") } alt="topImg" />
        <div className="links-list">
          <div className="links-list-top">
            <img src={ require("../../../img/a21.png") } alt="aa" />
          </div>
          <div className="zhuanshuCode">
            <span>{ YQcode }</span>
            <button onClick={ this.copyYQcode }></button>
          </div>
          <Input disabled={true} style={{ width: '100%', fontSize: '1rem' }} placeholder={ this.state.placeholder } />
          <button onClick={ this.cloneBtn } className="download-btn">APP</button>
          <Popover
            content={content}
            title={text}
            trigger="click"
            visible={this.state.visible}
            onVisibleChange={this.handleVisibleChange}
          >
            <div className="shouyi">
              <div>
                <Icon type="question-circle" theme="twoTone" />
                <p></p>
              </div>
            </div>
          </Popover>
          <ul>
            <li>
              <span></span>
              <span>{ money }</span>
            </li>
            <li>
              <span></span>
              <span>{ use_num }</span>
            </li>
            <li>
              <span></span>
              <span>{ active_num }</span>
            </li>
            <li>
              <span></span>
              <span>{ second_use_num }</span>
            </li>
            <li>
              <span></span>
              <span>{ second_active_num }</span>
            </li>
          </ul>
        </div>
      </div>
    )
  }
}

export default TuiJian
