import React,{ Component } from 'react';
import { Link } from 'react-router-dom';
import { Icon, Input, message } from 'antd';
import axios from 'axios';
import copy from 'copy-to-clipboard';             //

import '../../myCenter/tuiJian/tuijian.css';
import '../../../component/apis';

// const text = <span></span>;
// const content = (
//   <div className="contents">
//     <p><Icon type="check-circle" theme="twoTone" /></p>
//     <p><Icon type="check-circle" theme="twoTone" />1-3</p>
//     <p><Icon type="check-circle" theme="twoTone" />0.3-88</p>
//     <p><Icon type="check-circle" theme="twoTone" /></p>
//     <p><Icon type="check-circle" theme="twoTone" /><span style={{ color: '#2897ff', fontWeight: 'bold' }}>10%5%</span></p>
//   </div>
// );

class ActivityPage extends Component {
  constructor() {
    super();
    this.state= {
      visible: false,
    }
  }

  UNSAFE_componentWillMount() {
    axios.get(global.constants.website+'/api/index/action_invite', {
      headers: {AppAuthorization: localStorage.getItem("token")}        //post  token
    })
    .then( res => {
      let datas = res.data.data;
      // console.log(datas);
      this.setState({
        YQcode: datas.code,                                 //
        active_num: datas.active_num,                       //
        use_num: datas.use_num,                             //
        placeholder: datas.share_link,                     //
      })
    })
    .catch(function(error) {
      console.log(error);
    });
  }

  // APP
  cloneBtn = (e) => {
    copy(this.state.placeholder);        //
    message.success('');
  }

  // 
  copyYQcode = () => {
    copy(this.state.YQcode);             //
    message.success('');
  }

  // 
  hide = () => {
    this.setState({
      visible: false,
    });
  }
  handleVisibleChange = (visible) => {
    this.setState({ visible });
  }

  render() {
    const { YQcode, active_num, use_num } = this.state;
    return(
      <div style={{ backgroundColor: '#ed3c0e' }}>
        <header className="tabTitle">
          <div className="return"><Link to="/taskHallPage"><Icon type="left" theme="outlined" /></Link></div>
          
        </header>
        <img className="top-img" src={ require("../../../img/activity.png") } alt="topImg" />
        <div className="links-list" style={{ paddingBottom: '1.5rem' }}>
          <div className="links-list-top">
            <img src={ require("../../../img/a22.png") } alt="aa" />
          </div>
          <div className="zhuanshuCode">
            <span>{ YQcode }</span>
            <button onClick={ this.copyYQcode } style={{ backgroundColor: '#ed3c0e', marginTop: '0' }}></button>
          </div>
          <Input disabled={true} style={{ width: '100%', fontSize: '1rem' }} placeholder={ this.state.placeholder } />
          <button onClick={ this.cloneBtn } className="download-btn1">APP</button>
          <div className="contents" style={{ padding: '15px', fontSize: '15px' }}>
            <p style={{ textAlign: 'center', fontWeight:'bold', paddingBottom:'7px' }}>610</p>
            <p><Icon type="check-circle" theme="twoTone" /><span className="spanStyle">10</span><span className="spanStyle">10</span></p>
            <p><Icon type="check-circle" theme="twoTone" /><span className="spanStyle">30</span><span className="spanStyle">36</span></p>
            <p><Icon type="check-circle" theme="twoTone" /><span className="spanStyle">50</span><span className="spanStyle">75</span></p>
            <p><Icon type="check-circle" theme="twoTone" /><span className="spanStyle"></span>.....</p>
          </div>
          {/* <Popover
            content={content}
            title={text}
            trigger="click"
            visible={this.state.visible}
            onVisibleChange={this.handleVisibleChange}
            >
            <div className="shouyi">
              <div>
            <Icon type="question-circle" theme="twoTone" />
            <p></p>
              </div>
            </div>
          </Popover> */}
          <ul>
            <li>
              <span></span>
              <span>{ use_num }</span>
            </li>
            <li>
              <span></span>
              <span>{ active_num }</span>
            </li>
          </ul>
          <div style={{ paddingTop: '15px' }}>
            <p><span style={{ color: '#ed3c0e', fontSize: '20px' }}>*</span> </p>
            <p><span style={{ color: '#ed3c0e', fontSize: '20px' }}>*</span></p>
            <p><span style={{ color: '#ed3c0e', fontSize: '20px' }}>*</span>3 </p>
            <p><span style={{ color: '#ed3c0e', fontSize: '20px' }}>*</span></p>
            <p><span style={{ color: '#ed3c0e', fontSize: '20px' }}>*</span></p>
          </div>
        </div>
      </div>
    )
  }
}

export default ActivityPage
