import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Icon, Form, Input, Button, message } from 'antd';
import axios from 'axios';
import lrz from 'lrz';
// import ImagePicker from 'antd-mobile/lib/image-picker';
import ActivityIndicator from 'antd-mobile/lib/activity-indicator';
import WingBlank from 'antd-mobile/lib/wing-blank';
import '../../../../component/apis';

const FormItem = Form.Item;
const imgdata = [];         //1
const imgdata1 = [];        //2
// 
const cards = /^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/;

class certifications extends Component {
  constructor() {
    super();
    this.state = {
      animating: false,
      files01: imgdata,       //1
      files02: imgdata1,      //2
      num: 0,                 //false true
      num1: 0,                //false true
    }
  }

  componentDidMount() {
    let this_ = this;
    axios.get(global.constants.website+'/api/index/realnamechange',
    {
      headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
    })
    .then(function (response) {   //
      let responses = response.data.data;
      let cardid_img = [responses.cardid_img];        //
      let cardid_img2 = [responses.cardid_img2];      //
      console.log(responses);
      // console.log(cardid_img2);
      this_.props.form.setFieldsValue({
        cardid_name: responses.cardid_name,
        cardid: responses.cardid,
      })
      this_.setState({
        files01: cardid_img,                             // 
        files02: cardid_img2,                            // 
        url1: responses.cardid_img.url,                  //
        url2: responses.cardid_img2.url,                 //
      })
    })
    .catch(function (error) {   //
      console.log(error);
    });
  }

  //  
  onUploadOne1 = (files01, type, index) => {
    // console.log(files01, type, index);
    if(type==='add'){
      lrz(files01[0].url, {quality:0.5})
        .then((rst)=>{
            // 
            // console.log(rst)
            // console.log(rst.base64);
            this.setState({
              tu1: rst.base64
            })
          })
    }else{
      this.setState({imagesrc01:''})
    }
    this.setState({
      files01,
      num: 1,                  // true
    });
  }
  //  
  onUploadOne2 = (files02, type, index) => {
    // console.log(files02, type, index);
    if(type==='add'){
      lrz(files02[0].url, {quality:0.5})
        .then((rst)=>{
            // 
            // console.log(rst)
            // console.log(rst.base64);
            this.setState({
              tu2: rst.base64
            })
          })
    }else{
      this.setState({imagesrc01:''})
    }
    this.setState({
      files02,
      num1: 1,                  // true
    });
  }

  // ajax
  handleSubmit = (e) => {
    e.preventDefault();
    let this_ = this;
    let states = this.state;
    if ( states.num === 1 && states.num1 === 1 ) {          // numnum1true 
      var imgs1 = [states.tu1, states.tu2];                 //
    } else if ( states.num1 ) {                             //num1 srue 
      var img2 = [states.url1, states.tu2]                  // 
    } else if ( states.num ) {                              //num srue 
      var img1 = [states.tu1, states.url2]                  // 
    } else {
      var fImgs = [states.url1, states.url2];                //
      var imgs = [states.tu1, states.tu2];                   //
      imgs = fImgs;                                          //
    }
    // console.log(imgs);
    this.props.form.validateFields((err, values) => {
      if ( !err === true ) {
        if ( !cards.test(values.cardid) ) {                                   //
          message.error("")
        } else {
          this_.setState({ animating: true })                                 //login.....
          // let imgs = [values.images[0].url, values.images[1].url];            //
          axios.post(global.constants.website+'/api/index/realnamecommit', {
            images: states.num === 1 && states.num1 === 1 ? imgs1 : ( states.num1 === 1 ? img2 : ( states.num === 1 ? img1 : imgs ) ),        //                                                     //
            realName: values.cardid_name,                                        //
            cardno: values.cardid,                                            //
          },
          {
            headers: {AppAuthorization: localStorage.getItem("token")}        //post  token
          })
          .then( response => {
            let data_ = response.data;
              // console.log(data_);
            if ( data_.status ) {                           //status true 
              this_.setState({ animating: false })          //login.....
              message.success(data_.msg);
              this_.props.history.push("/personal");
            } else {                                        //status false 
              this_.setState({ animating: false })          //login.....
              message.error(data_.msg);
            }
          })
          .catch( error => {
            console.log(error);
          })
        }
      }else {
        message.error('');
      }
    });
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    const { animating } = this.state;
    return(
      <div>
        <header className="tabTitle">
          <div className="return"><Link to="/personal"><Icon type="left" theme="outlined" /></Link></div>
          
        </header>
        <div className="buyAdmin-box">
          <WingBlank>
            <Form onSubmit={this.handleSubmit} className="login-form">
              <FormItem
                label=""
              >
                {getFieldDecorator('cardid_name', {
                  rules: [{ required: true, message: '!' }],
                })(
                  <Input className="buy-input" placeholder="" type="text" />
                )}
              </FormItem>
              <FormItem
                label=""
              >
                {getFieldDecorator('cardid', {
                  rules: [{ required: true, message: '!' }],
                })(
                  <Input className="buy-input" type="text" placeholder="" maxLength={18} />
                )}
              </FormItem>
              {/* <FormItem
                label=""
                >
                {getFieldDecorator('images1', {
                  rules: [{ required: localStorage.getItem("realname_status") === "0" ? true : false , message: '!' }],
                })(
                  <ImagePicker
                length={1}
                files={files01}
                multiple={false}
                onChange={this.onUploadOne1}
                onImageClick={(index, fs) => console.log(index, fs)}
                selectable={files01.length < 1}
                  />
                )}
                </FormItem>
                <FormItem
                label=""
                >
                {getFieldDecorator('images2', {
                  rules: [{ required: localStorage.getItem("realname_status") === "0" ? true : false , message: '!' }],
                })(
                  <ImagePicker
                length={1}
                files={files02}
                onChange={this.onUploadOne2}
                onImageClick={(index, fs) => console.log(index, fs)}
                selectable={files02.length < 1}
                  />
                )}
              </FormItem> */}
              <div style={{ color:"red", paddingBottom: '0.8rem' }}>
                
              </div>
              <Button type="primary" htmlType="submit" className="accountBtn"></Button>
              <div className="toast-example">
                <ActivityIndicator
                  toast
                  text="..."
                  animating={animating}
                />
              </div>
            </Form>
          </WingBlank>
        </div>
      </div>
    )
  }
}

const certification = Form.create()(certifications);
export default certification
