import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { Drawer, Button, Menu, Icon, message } from 'antd';
import '../../../component/apis';

class TaskState extends Component {
  constructor(props, context) {
    super(props, context);
    // console.log(props);
    this.state = {
      visible: false,
      collapsed: false,
      itme_key: null    // key
    }
  }

  componentDidMount() {
    //
    let time = this.props.time;   //
    let this_ = this;
    const timer=setInterval(function(){
        var hour=0, minute=0, second=0;//
        if(time > 0){
            hour = Math.floor(time / (60 * 60));
            minute = Math.floor(time / 60) - (hour * 60);
            second = Math.floor(time) - (hour * 60 * 60) - (minute * 60);
        }

        if (hour <= 9) hour = '0' + hour;
        if (minute <= 9) minute = '0' + minute;
        if (second <= 9) second = '0' + second;
        //
        const cuttime = hour+":"+minute+":"+second;
        // console.log(cuttime);
        // 
        this_.setState({
          cuttime: cuttime
        })
        time--;
    },1000);
    if( time <= 0 ){
        clearInterval(timer);
    }
  }
  componentWillUnmount = () => {
    this.setState = (state,callback)=>{
      return;
    };
}

  // 
  showDrawer = () => {
    this.setState({
      visible: true
    })
  }
  //  
  onClose = () => {
    this.setState({
      visible: false,
    });
    // console.log("");
  };
  //  
  onConfirm = () => {
    let dataState = this.state;
    let dataProps = this.props;
    let this_ = this;
    if ( dataState.itme_key === null ) {
      message.error("")
    } else {
      axios.post(global.constants.website+'/api/task/cancleTask', {
        order_id: dataProps.order_id,           //ID
        backout_type: dataState.itme_key,       //
        backout_cause: dataState.itme_key === "1" ? "" : ( dataState.itme_key === "2" ? "" : (dataState.itme_key === "3" ? "" : "") )
      },{
        headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
      })
      .then( res => {
        // console.log(res);
        if ( res.data.status ) {
          this.setState({
            visible: false,
          });
          this_.props.history.push('/myTask');
          message.success(res.data.msg);
        } else {
          message.error(res.data.msg);
        }
      })
      .catch( error => {
        console.log(error);
      })
    }
  }

  render() {
    // order_message
    const { task_type, remark, order_status, tasktype_pic, ordertatusText, remark_pic, is_muti_keyword,shop_around_time } = this.props;
    const { cuttime } = this.state;
    // console.log(remark_pic);
    return(
      <section className="taskDetail-state">
        <img src={tasktype_pic} alt="taobaoImg" />
        <span style={{ fontSize: '1rem' }}>{ordertatusText} </span>
        <span style={{ color: 'red' }}> { order_status ? "" : <span><b>{cuttime}</b>(1)</span> }</span>
        <p></p>
        <div>
          <p style={{ fontSize: '1rem', color: '#0156B1' }}>{remark}</p>
          {
            remark_pic === "" && remark === "" ?
              <p></p>
            :
            remark_pic !== "" ?
              remark_pic.map((item, index) => {
                return(
                  <img style={{ maxWidth: '100%', marginBottom:'0.3rem' }} key={index} src={item} alt=""/>
                )
              })
            :
            ""
          }
        </div>
        {/* <p>{order_message}</p> */}
        <div className="start-task-btn">
          <button className="tasktn" onClick={this.showDrawer}></button>
          {/* <button className="tasktns"><Link to={ is_muti_keyword === 1 ? { pathname: "/taskStateChilds", state: remark_pic } : { pathname: "/taskStateChild", state: remark_pic }}></Link></button> */}
          {/* shop_around_time  1  */}
          {
            order_status ?
              ""
            :
            shop_around_time ?
              <button className="stateBtns"><Link to={{ pathname: "/taskStateChild", state: remark_pic }}></Link></button>
            :
            <button className="stateBtns"><Link to={ is_muti_keyword === 1 ? { pathname: "/taskStateChilds", state: remark_pic } : { pathname: task_type === "1"?"/lookTaskStateChild":"/taskStateChild", state: remark_pic }}></Link></button>
          }
          <button className="tasktn"><Link to="/appealTask"></Link></button>
        </div>
        <Drawer
          title=""
          width={"90%"}
          placement="right"
          onClose={this.onClose}
          maskClosable={false}
          visible={this.state.visible}
          style={{
            height: 'calc(100% - 55px)',
            overflow: 'auto',
            paddingBottom: 53,
            padding:0
          }}>
          <Menu
            defaultOpenKeys={['sub1']}
            mode="inline"
            theme="white"
            inlineCollapsed={this.state.collapsed}
            onClick={item => {this.setState({itme_key: item.key})}}  //key 
          >
            <Menu.Item key="1">
              <Icon type="pie-chart" theme="twoTone" />
              <span></span>
            </Menu.Item>
            <Menu.Item key="2">
              <Icon type="stop" theme="twoTone" />
              <span></span>
            </Menu.Item>
            <Menu.Item key="3">
              <Icon type="frown" theme="twoTone" />
              <span></span>
            </Menu.Item>
            <Menu.Item key="5">
              <Icon type="tool" theme="twoTone" />
              <span></span>
            </Menu.Item>
          </Menu>
          <div
            style={{
              position: 'absolute',
              bottom: 0,
              width: '100%',
              borderTop: '1px solid #e8e8e8',
              padding: '10px 16px',
              textAlign: 'right',
              left: 0,
              background: '#fff',
              borderRadius: '0 0 4px 4px',
            }}>
            <Button style={{ marginRight: 8,}} onClick={this.onClose}></Button>
            <Button onClick={this.onConfirm} type="primary"></Button>
          </div>
        </Drawer>
      </section>
    )
  }
}

export default TaskState
