import React, { Component } from 'react';
import { Icon, Button, message } from 'antd';
import axios from 'axios';
import lrz from 'lrz';
import ImagePicker from 'antd-mobile/lib/image-picker';
import { Link } from 'react-router-dom';
import ActivityIndicator from 'antd-mobile/lib/activity-indicator';
import WingBlank from 'antd-mobile/lib/wing-blank';

import './goodPingjia.css';
import '../../../../component/apis';

const data = [];
const imgdata = [];         //1
const imgdata1 = [];        //2

//
message.config({
  top: 300,
});

class GoodPingJia extends Component {
  constructor(props) {
    super(props);
    this.state = {
      animating: false,
      files: data,
      files01: imgdata,       //1
      files02: imgdata1,      //2
    }
    // console.log(props);
  }

  UNSAFE_componentWillMount() {
    let this_ = this;
    if ( this.props.location.state === undefined ) {
      message.warning('');
      this_.props.history.push("/myTask")
    } else {
      axios.post(global.constants.website+'/api/task/receivetask', {
        order_id: this.props.location.state,
      },{
        headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
      })
      .then( res=> {
        let datas = res.data.data;
        // console.log(datas);
        this.setState({
          order_id: datas.order_id,               //order_id
          platformname: datas.platformname,       //
          keywords_pic: datas.keywords_pic,       //
          pic_content: datas.pic_content,         //
          keyword_types: datas.keyword_types,     //
        })
      })
      .catch(error => {
        console.log(error);
      })
    }
  }

  //  
  // onChange = (files, type, index) => {
  //   console.log(files);
  //   this.setState({
  //     files,
  //   });
  // }
  //  
  onImageChange01 = (files01, type, index) => {
    // console.log(files01, type, index);
    if(type==='add'){
      lrz(files01[0].url, {quality:0.1})
        .then((rst)=>{
            // 
            // console.log(rst)
            // console.log(rst.base64);
            this.setState({
              tu1: rst.base64
            })
          })
    }else{
        this.setState({imagesrc01:''})
    }
    this.setState({
        files01,
    });
  }
  onImageChange02 = (files02, type, index) => {
    // console.log(files02, type, index);
    if(type==='add'){
      lrz(files02[0].url, {quality:0.1})
        .then((rst)=>{
            // 
            // console.log(rst)
            // console.log(rst.base64);
            this.setState({
              tu2: rst.base64
            })
          })
    }else{
        this.setState({imagesrc01:''})
    }
    this.setState({
        files02,
    });
  }
  //  ajax
  uploadShouHuo = () => {
    // let photos = this.state.files01;        //
    let this_ = this;
    let order_id = this.state.order_id;
    let imgs = [this.state.tu1, this.state.tu2];    //
    // console.log(imgs);
    if ( imgs[0] !== undefined && imgs[1] !== undefined ) {
      this_.setState({ animating: true })            //login.....
      axios.post(global.constants.website+'/api/task/receivetaskcommit',{
        order_id: order_id,                         //ID
        receive_evaluate_content: imgs,             //
      },{
        headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
      })
      .then( res => {
        // console.log(res.data.status);
        if ( res.data.status ) {
          this_.setState({ animating: false })          //login.....
          this_.props.history.push("/myTaskDetails");
          message.success(res.data.msg);
        } else {
          this_.setState({ animating: false })          //login.....
          message.error(res.data.msg)
        }
      })
      .catch( error => {
        console.log(error);
      })
    }else {
      //ajax
      message.error('');
    }
  }


  render() {
    const { files01,files02,platformname,animating,keyword_types,keywords_pic,pic_content } = this.state;
    return(
      <div>
        <header className="tabTitle">
          <div className="return"><Link to="/myTaskDetails"><Icon type="left" theme="outlined" /></Link></div>
          
        </header>
        <div>
          <WingBlank>
            <div className="goodPingJia-box">
              <h2> !</h2>
              <p> {platformname}</p>
              <div className="goodPingJia-box-child">
                <p>({platformname})</p>
                {
                  keyword_types > 1 ?
                    <img style={{ maxWidth: '100%' }} src={keywords_pic[0]} alt="tu"/>
                  :
                  <p style={{ backgroundColor: '#fff',textAlign: 'center' }}></p>
                }
                <p>({platformname})</p>
                {
                  keyword_types > 2 ?
                    pic_content.map((item, index) => {
                      return(
                        <img key={index} style={{ maxWidth: '100%' }} src={item} alt="tu"/>
                      )
                    })
                  :
                  <p style={{ backgroundColor: '#fff' }}></p>
                }
              </div>
              <p> </p>
              {/* <ImagePicker
                length={2}
                files={files}
                onChange={this.onChange}
                onImageClick={(index, fs) => console.log(index, fs)}
                selectable={files.length < 2}
              /> */}
              <ImagePicker
                length={1}
                files={files01}
                onChange={this.onImageChange01}
                onImageClick={(index, fs) => console.log(index, fs)}
                selectable={files01.length < 1}
                multiple={false}
              />
              <ImagePicker
                length={1}
                files={files02}
                onChange={this.onImageChange02}
                onImageClick={(index, fs) => console.log(index, fs)}
                selectable={files02.length < 1}
                multiple={false}
              />
              <p><span style={{ fontWeight:'bold',fontSize:'1rem',color:'red' }}></span><span style={{ fontWeight:'bold',fontSize:'1rem',color:'red' }}></span></p>

            </div>
            <Button type="primary" className="login-form-button" style={{ margin: '2rem 0 1rem 0' }} onClick={this.uploadShouHuo}>
              
            </Button>
            <div className="toast-example">
              <ActivityIndicator
                toast
                text="..."
                animating={animating}
              />
            </div>
          </WingBlank>
        </div>
      </div>
    )
  }
}

export default GoodPingJia
