import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import { Icon, Form, Input, Button, Row, Col, message, Modal } from 'antd';
import axios from 'axios';

// import Hongbao from '../../../img/hongbao.png';
import './register.css';
import '../../../component/apis';

const FormItem = Form.Item;
let phoneNumber = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/; //

class RegisterPages extends Component {
  constructor(props) {
    super(props);
    this.state = {
      getCodesState: true,
      codeNum: 60,        // 60 
      TestGetCode: "",
      placeholder: "",
      tuCodeLink: null,   //
      tuCode: null,       //
      sid: null,          // sid
      time: null,         //time
      // hongbao: false,     //
      // openhonbao: false,  //
      // visible: false,      //
      // register_money: null  //
    }
  }

  //  
  componentDidMount() {
    axios.get(global.constants.website+'/api/user/getcaptcha')
    .then(response => {
      // console.log(response);
      if ( response.status === 200 ) {
        this.setState({
          tuCodeLink: response.data.data.captcha_src,
          sid: response.data.data.sid,
        })
      } else {
        message.error("")
      }
      // console.log(response.data.data);
    })
    .catch(error => {
      console.log(error);
    })
  }
  componentWillUnmount = () => {
    this.setState = (state,callback)=>{
      return;
    };
}

  //  
  getVerifyCode = () => {
    axios.get(global.constants.website+'/api/user/getcaptcha')
    .then(response => {
      this.setState({
        tuCodeLink: response.data.data.captcha_src,
        sid: response.data.data.sid,
      })
    })
    .catch(error => {
      console.log(error);
    })
    // console.log(this.state.tuCode);
  }

  //  onChange placeholder 
  onChange = (e) => {
    this.setState({placeholder: e.target.value})
  }
  // 
  tuCodes = (e) => {
    this.setState({tuCode: e.target.value})
  }

  // 
  getCodes = () => {
    let dataCode_ = this.state;
    let this_ = this;   //  this
    if (dataCode_.placeholder === "") {
      message.error("");
    } else if (!phoneNumber.test(dataCode_.placeholder)) {
      message.error("");
    } else if (dataCode_.tuCode === null) {
      message.error("");
    } else {
      // ajax
      axios.post(global.constants.website+'/api/user/sendcode', {
        sid: dataCode_.sid,
        tuCode: dataCode_.tuCode,
        phoneNum: dataCode_.placeholder
      })
      .then(function (response) {   //
        // console.log(response.data);
        //  status  true  
        if ( response.data.status ) {
          //  
          let codeNum = dataCode_.codeNum;
          const timer = setInterval(() => {
          this_.setState({
            getCodesState:false,
            codeNum: (codeNum--)
            }, () => {
                if (codeNum === 0) {
                clearInterval(timer);
                this_.setState({
                  getCodesState: true,
                  codeNum: 60,
                  TestGetCode: ""
                })
              }
            })
          }, 1000)
          // 
          message.success(response.data.msg);
        } else {  //  status  falseelse
          // 
          message.error(response.data.msg);
        }
      })
      .catch(function (error) {   //
        console.log(error);
      });
      }
    }
    // login
    newBtn = () => {
      window.location.reload();
    }

    //  
    handleSubmit = (e) => {
      e.preventDefault();
      let this_ = this;
      this.props.form.validateFieldsAndScroll((err, values) => {
        if (!err) {
          // console.log('Received values of form: ', values);
          if (!phoneNumber.test(values.phoneNum)) {
            message.error("")
          } else if (values.loginPassword !== values.aloginPassword) {
            message.error("")
          } else {
            // console.log(values);
            // ajax
            axios.post(global.constants.website+'/api/user/register', {
              // 
              QQnum: values.QQnum,
              phoneNum: values.phoneNum,
              loginpassword: values.loginPassword,
              aloginpassword: values.aloginPassword,
              tuCode: values.tuCode,
              captcha: values.captcha,
              yqcode: values.yqcode
            })
            .then(function (response) {   //
              // console.log(response.data);
              // response.data true
              if ( response.data.status ) {
                // token
                localStorage.setItem("token", response.data.token);
                // message.success(response.data.msg);
                // 
                // this_.setState({
                //   hongbao: true,
                //   register_money: response.data.data.register_money,
                // })
                // 
                message.success(response.data.msg, successSkip => {
                  this_.props.history.push('/taskHallPage')
                })
              } else {  // response.data false
                message.error(response.data.msg)
              }
            })
            .catch(function (error) {   //
              console.log(error);
            });
            // console.log(values);
          }
        }
      });
    }

    // 
    handleCancel = () => {
      this.setState({hongbao: false})
    }

    // 
    openBtn = () => {
      this.setState({openhonbao: true});
      setTimeout( () => {
  			this.setState({openhonbao: false, visible: true, hongbao:false});
  		},2000);
    }

    // 
    gotoTaskHall = () => {
      this.props.history.push('/taskHallPage')
    }

    // 
    gotoCash = () => {
      this.props.history.push('/')
    }

    render() {// hongbao, openhonbao,
      const { visible, register_money } = this.state;
      const {getFieldDecorator} = this.props.form;
      const formItemLayout = {
        labelCol: {
          xs: {
            span: 24
          },
          sm: {
            span: 8
          }
        },
        wrapperCol: {
          xs: {
            span: 24
          },
          sm: {
            span: 16
          }
        }
      };
      const tailFormItemLayout = {
        wrapperCol: {
          xs: {
            span: 24,
            offset: 0
          },
          sm: {
            span: 16,
            offset: 8
          }
        }
      };

      return (<div>
        <header className="tabTitle">
          <div onClick={this.newBtn} className="return">
            <Link to="/"><Icon type="left" theme="outlined"/></Link>
          </div>
          
        </header>
        <Form onSubmit={this.handleSubmit} style={{
            padding: "3.7rem 1.5rem 0 1.5rem"
        }}>
          <FormItem {...formItemLayout} label="">
            {
              getFieldDecorator('phoneNum', {
                rules: [
                {
                    required: true,
                    message: '!'
                }
                ]
              })(<Input className="register-input" onChange={this.onChange} maxLength={11} placeholder={this.state.placeholder}/>)
            }
          </FormItem>
          <FormItem {...formItemLayout} label="">
            {/* <div>
              <img onClick={ this.getVerifyCode } style={{
              width: "100%"
              }} src={this.state.tuCodeLink} alt=""/>
            </div> */}
            <div style={{ display: 'flex' }}>
              <img style={{ width: "80%" }} src={ this.state.tuCodeLink } alt=""/>
              <Button style={{ height: 'auto' }} onClick={ this.getVerifyCode }></Button>
            </div>
            {
              getFieldDecorator('tuCode', {
                rules: [
                {
                    required: true,
                    message: '!'
                }
                ]
              })(<Input className="register-input" onChange={ this.tuCodes } placeholder=""/>)
            }
          </FormItem>
          <FormItem {...formItemLayout} label="">
            <Row gutter={8}>
              <Col span={16}>
                {
                  getFieldDecorator('captcha', {
                    rules: [
                    {
                        required: true,
                        message: '!'
                    }
                    ]
                  })(<Input className="register-input" placeholder=""/>)
                }
              </Col>
              <Col span={8}>
                {
                  this.state.getCodesState
                    ? <Button style={{ marginTop: '0' }} onClick={this.getCodes}>{ this.state.TestGetCode }</Button>
                    : <Button style={{ marginTop: '0' }} disabled="disabled">{this.state.codeNum}</Button>
                }
              </Col>
            </Row>
          </FormItem>
          <FormItem {...formItemLayout} label="">
            {
              getFieldDecorator('loginPassword', {
                rules: [
                {
                    required: true,
                    message: '!'
                }
                ]
              })(<Input className="register-input" placeholder="6-16"/>)
            }
          </FormItem>
          <FormItem {...formItemLayout} label="">
            {
              getFieldDecorator('aloginPassword', {
                rules: [
                {
                    required: true,
                    message: '!'
                }
                ]
              })(<Input className="register-input" placeholder="6-16"/>)
            }
          </FormItem>
          <FormItem {...formItemLayout} label="">
            {
              getFieldDecorator('yqcode', {
                rules: [
                {
                    required: true,
                    message: '!'
                }
                ]
              })(<Input className="register-input" placeholder=""/>)
            }
          </FormItem>
          <FormItem {...formItemLayout} label="QQ" style={{ marginBottom: '4rem' }}>
            {
              getFieldDecorator('QQnum', {
                rules: [
                {
                    required: true,
                    message: 'QQ!'
                }
                ]
              })(<Input className="register-input" type="Number" placeholder="QQ"/>)
            }
          </FormItem>
          <FormItem {...tailFormItemLayout}>
            <Button className="btn-buy" type="primary" htmlType="submit"></Button>
          </FormItem>
        </Form>

        {/*  */}
        {/* <Modal className={ openhonbao ? "shake" : "" } closable={false} visible={hongbao} footer={null} onCancel={this.handleCancel}>
          <img onClick={ this.openBtn } alt="" style={{ width: '100%' }} src={Hongbao} />
        </Modal> */}

        {/*  */}
        <Modal
          title=""
          visible={visible}
          onOk={this.gotoTaskHall}
          onCancel={this.gotoCash}
          okText={""}
          cancelText={""}
        >
          <p>{ register_money }</p>
        </Modal>

      </div>)
    }
  }

  const RegisterPage = Form.create()(RegisterPages);
  export default RegisterPage
