import React, { Component } from 'react';
import { Carousel } from 'antd';
import axios from 'axios';

import '../../../component/apis';

class UserCashList extends Component {
  constructor() {
    super();
    this.state = {

    }
  }

  componentDidMount() {
    // let this_ = this;
    axios.get(global.constants.website+'/api/index/withdrawalList',{headers: {AppAuthorization: localStorage.getItem("token")}})   //
    .then(response => {
      let data_s = response.data.data;
      // console.log(data_s);
      this.setState({
        withdrawalList: data_s,
      })
    })
    .catch(error => {
      console.log(error);
    });
  }

  render() {
    const{ withdrawalList } = this.state;
    // const userNumArr = ["156***1180 50.00", "187***9110 400.00", "136***1890 15.00", "199***8262 99.00", "186***2250 150.00"]; //
    return(//speed={5000}
      <Carousel autoplay={true} dots={false} autoplaySpeed={3000}>
        {
          withdrawalList ?
            withdrawalList.map((item,index) => {
              return (
                <div className="user-cash-list" key={index}>
                  <span>{ item.mobile } </span>
                  <span>{ item.withdrawal_amount }</span>
                </div>
              )
            })
          :
          <div className="user-cash-list">
            <h3></h3>
          </div>
        }
      </Carousel>
    )
  }
}

export default UserCashList
