import React, { Component } from 'react';
import { Icon, Badge,message,Button } from 'antd';
import { Link } from 'react-router-dom';
import axios from 'axios';    //ajax

import './myCenter.css';
import '../../component/apis';
import RouteTabComponent from '../../component/routeTab/routeTab';  //tabs

class MyCenterPage extends Component {
  constructor() {
    super();
    this.state = {
      allMoney: 0,      //
    }
  }

  //  ajax 
  UNSAFE_componentWillMount() {
    let this_ = this;
    axios.get(global.constants.website+'/api/index/index',{headers: {AppAuthorization: localStorage.getItem("token")}})   //
    .then(response => {
      let data_s = response.data.data;
      // console.log(data_s);
      if ( response.data.status === "_0001" ) {
          message.success(response.data.msg, successSkip => {
          this_.props.history.push("/");
        })
      } else {
        this.setState({
          complain_count: data_s.complain_count,                    //	
          total_commission: Number(data_s.total_commission),        //  
          money_account: data_s.money_account,                      //  
          commission_account: data_s.commission_account,            //  
          tbbind_count: data_s.tbbind_count,                        //  
        })
      }
    })
    .catch(error => {
      console.log(error);
    });
  }
  // 
  componentWillUnmount = () => {
    this.setState = (state,callback)=>{
      return;
    };
  }

  // 
  benMoney = () => {
    let states = this.state;
    if ( states.money_account < 1 ) {
      message.warning('1');
    } else {
      axios.post(global.constants.website+'/api/index/moneywithdraw', {
        type: 1,                                    //1
        money: Number(states.money_account),        //
      },
      {
        headers: {AppAuthorization: localStorage.getItem("token")}        //post  token
      })
      .then(response => {
        let data_s = response.data;
        if ( data_s.status ) {
          this.setState({
            total_commission: states.total_commission + Number(states.money_account),
            money_account: 0,
          })
        } else {
          message.warning('');
        }
      })
      .catch(error => {
        console.log(error);
      });
    }
  }
  // 
  yongMoney = () => {
    let states = this.state;
    if ( states.commission_account < 10 ) {
      message.warning('10');
    } else {
      axios.post(global.constants.website+'/api/index/moneywithdraw', {
        type: 2,                                    //1
        money: Number(states.commission_account),        //
      },
      {
        headers: {AppAuthorization: localStorage.getItem("token")}        //post  token
      })
      .then(response => {
        let data_s = response.data;
        console.log(data_s);
        if ( data_s.status ) {
          this.setState({
            total_commission: states.total_commission + Number(states.commission_account),
            commission_account: 0,
          })
        } else {
          message.warning(data_s.msg);
        }
      })
      .catch(error => {
        console.log(error);
      });
    }
  }

  // , tbbind_count
  render() {
    const { complain_count, total_commission, money_account, commission_account } = this.state;
    return(
      <div>
        {/* top */}
        <div className="myCenter-top">
          <p></p>
          <p>{ total_commission }</p>
          <div className="myCenter-top-button">
            <div>
              {/* <p>{ money_account }</p> */}
              <p> <span className="moneySize">{ money_account }</span></p>
              <Button style={{ lineHeight: '16px' }} onClick={ this.benMoney }></Button>
            </div>
            <div>
              {/* <p>{ commission_account }</p> */}
              <p> <span className="moneySize">{ commission_account }</span></p>
              <Button style={{ lineHeight: '16px' }} onClick={ this.yongMoney }></Button>
            </div>
          </div>
        </div>
        {/*   */}
        <div>
          <div className="myCenter-tixian">
            <div>
              <Link to="/cash">
                <Icon style={{ fontSize: '1.2rem', paddingRight: '0.3rem' }} type="inbox" />
                <span></span>
              </Link>
            </div>
            <div>
              <Link to="/commission">
                <Icon style={{ fontSize: '1.2rem', paddingRight: '0.3rem' }} type="dollar" />
                <span></span>
              </Link>
            </div>
          </div>
        </div>
        {/*  */}
        <ul className="lists">
          <li>
            <Link className="myCenter-A" to="/buyAdmin">
              <div>
                <Icon type="user" />
                <span></span>
              </div>
              <div>
                <Icon type="right" />
              </div>
            </Link>
          </li>
          <li>
            <Link className="myCenter-A" to="/personal">
              <div>
                <Icon type="smile" />
                <span></span>
              </div>
              <div>
                <Icon type="right" />
              </div>
            </Link>
          </li>
          {/* <li>
            <Link className="myCenter-A" to="/tuiJian">
              <div>
            <Icon type="like" />
            <span></span>
              </div>
              <div>
            <Icon type="right" />
              </div>
            </Link>
          </li> */}
          <li>
            <Link className="myCenter-A" to="/allCenter">
              <div>
                <Icon type="message" />
                <span></span>
              </div>
              <div>
                <Icon type="right" />
              </div>
            </Link>
          </li>
          <li>
            <Link className="myCenter-A" to="/shenSu">
              <div>
                <Icon type="dashboard" />
                <span></span>
                <Badge count={complain_count} style={{ marginLeft: '0.3rem', backgroundColor:'red' }} />
              </div>
              <div>
                <Icon type="right" />
              </div>
            </Link>
          </li>
          <li>
            <Link className="myCenter-A" to="/tongZhi">
              <div>
                <Icon type="cloud-download" />
                <span></span>
              </div>
              <div>
                <Icon type="right" />
              </div>
            </Link>
          </li>
        </ul>


        {/* tabs */}
        <RouteTabComponent />
      </div>
    )
  }
}

export default MyCenterPage
