import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Icon, Form, Input, Button, message } from 'antd';
import axios from 'axios';
import ActivityIndicator from 'antd-mobile/lib/activity-indicator';
import WingBlank from 'antd-mobile/lib/wing-blank';
import '../../../../component/apis';

const FormItem = Form.Item;
// let phoneNum = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/;   //

class TxPasswords extends Component {
  constructor() {
    super();
    this.state = {
      animating: false,
      code: null,           //val
    }
  }

  UNSAFE_componentWillMount() {
    //  
    axios.get(global.constants.website+'/api/user/getcaptcha')
    .then(response => {
      this.setState({
        tuCodeLink: response.data.data.captcha_src,
        sid: response.data.data.sid,
      })
    })
    .catch(error => {
      console.log(error);
    })
  }

  componentDidMount() {
    // 
    axios.get(global.constants.website+'/api/index/getmobilephone',
    {
      headers: {AppAuthorization: localStorage.getItem("token")}        //post  token
    })
    .then( response => {
      // console.log(response.data);
      this.props.form.setFieldsValue({
        numbers: response.data.data.phone,                //
      });
      this.setState({
        placeholder: response.data.data.phone
      })
    })
    .catch(function (error) {
      console.log(error);
    });
  }
  // 
  componentWillUnmount = () => {
    this.setState = (state,callback)=>{
      return;
    };
  }

  //  
  getVerifyCode = () => {
    axios.get(global.constants.website+'/api/user/getcaptcha')
    .then(response => {
      this.setState({
        tuCodeLink: response.data.data.captcha_src,
        sid: response.data.data.sid,
      })
    })
    .catch(error => {
      console.log(error);
    })
    // console.log(this.state.tuCode);
  }
  // 
  tuCodes = (e) => {
    this.setState({
      code: e.target.value
    })
  }

  // 
  duanXinCodeBtn = () => {
    let this_ = this;
    let datas = this.state;
    if ( datas.code === null ) {
      message.error("");
    } else {
      // 
      axios.post(global.constants.website+'/api/user/sendcode',
      {
        phoneNum: datas.placeholder,
        tuCode: datas.code,
        sid: datas.sid,
      },
      {
        headers: {AppAuthorization: localStorage.getItem("token")}        //post  token
      })
      .then( response=> {
        // console.log(response);
        if ( response.data.status ) {
          //  60
          let codeNum = datas.codeNum;
          const timer = setInterval(() => {
          this_.setState({
            getCodesState:false,
            phoneCode: response.data.code,
            codeNum: (codeNum--)
            }, () => {
                if (codeNum === 0) {
                clearInterval(timer);
                this_.setState({
                  getCodesState: true,
                  codeNum: 60,
                  TestGetCode: ""
                })
              }
            })
          }, 1000);
          message.success(response.data.msg);
        } else {
          message.error(response.data.msg);
        }
      })
      .catch( error=> {
        console.log(error);
      });
      // message.success("");
    }
  }

  // ajax
  handleSubmit = (e) => {
    e.preventDefault();
    let this_ = this;
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this_.setState({ animating: true });            //login.....
        axios.post(global.constants.website+'/api/index/changeCashPassword', {
          smscode: values.dongtaiCode,
          password: values.newPassword,
          repassword: values.okNewPassword,
        },{
          headers: {AppAuthorization: localStorage.getItem("token")}        //post  token
        })
        .then( response => {
          if ( response.data.status ) {
            this_.setState({ animating: false })          //login.....
            message.error(response.data.msg);
            this_.props.history.push('/personal')      //
          } else {
            this_.setState({ animating: false })          //login.....
            message.error(response.data.msg);
          }
        })
        .catch( error => {
          console.log(error);
        })
      }
    });
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    const { animating, tuCodeLink } = this.state;
    return(
      <div>
        <header className="tabTitle">
          <div className="return"><Link to="/personal"><Icon type="left" theme="outlined" /></Link></div>
          
        </header>
        <WingBlank>
          <Form onSubmit={this.handleSubmit} className="login-form" style={{ padding: '3.5rem 0.7rem 0 0.7rem' }}>
            <FormItem
              label=""
            >
              <div style={{ display: 'flex' }}>
                <img style={{ width: "80%" }} src={ tuCodeLink } alt=""/>
                <Button style={{ height: 'auto' }} onClick={ this.getVerifyCode }></Button>
              </div>
              {/* <img onClick={ this.getVerifyCode } style={{ width: "100%" }} src={ tuCodeLink } alt=""/> */}
              {getFieldDecorator('tpCode', {
                rules: [{ required: true, message: '!' }],
              })(
                <Input onChange={ this.tuCodes } className="buy-input" type="text" placeholder="" />
              )}
            </FormItem>
            <FormItem
              label=""
            >
              {getFieldDecorator('newPassword', {
                rules: [{ required: true, message: '!' }],
              })(
                <Input className="buy-input" placeholder="" maxLength={11} />
              )}
            </FormItem>
            <FormItem
              label=""
            >
              {getFieldDecorator('okNewPassword', {
                rules: [{ required: true, message: '!' }],
              })(
                <Input className="buy-input" type="text" placeholder="" />
              )}
            </FormItem>
            <FormItem
              label=""
            >
              {getFieldDecorator('numbers', {
                rules: [{ required: true, message: '!' }],
              })(
                <Input onChange={ this.numbersPlace } maxLength={11} className="buy-input" type="text" placeholder="" />
              )}
            </FormItem>
            <FormItem
              label=""
            >
              {getFieldDecorator('dongtaiCode', {
                rules: [{ required: true, message: '!' }],
              })(
                <Input className="buy-input" type="text" placeholder="" />
              )}
            </FormItem>
            <Button type="primary" onClick={this.duanXinCodeBtn} className="accountBtn"></Button>
            <Button type="primary" htmlType="submit" className="accountBtn"></Button>
            <div className="toast-example">
              <ActivityIndicator
                toast
                text="..."
                animating={animating}
              />
            </div>
          </Form>
        </WingBlank>
      </div>
    )
  }
}

const TxPassword = Form.create()(TxPasswords);
export default TxPassword
