import React, { Component } from 'react';
import { Icon, Drawer, Menu, Button,message } from 'antd';
import Modal from 'antd-mobile/lib/modal';
import { Link } from 'react-router-dom';
import axios from 'axios';

import './appealTask.css';
import '../../../../component/apis';

const prompt = Modal.prompt;
const alert = Modal.alert;

class AppealTaskDetails extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false,             //
      huifushensu: false,         //
      itme_key: null,             // key
    }
    // console.log(props.location.state.data);
  }

  UNSAFE_componentWillMount() {
    axios.post(global.constants.website+'/api/task/appealTaskDetail',{
      order_id: this.props.location.state.data,   //order_id
    },{
      headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
    })
    .then( res => {
      // console.log(res);
      let datas = res.data.data;
      this.setState({
        goodspic: datas.goodspic,                   //
        // seller_qq: datas.seller_qq,                 //QQ
        order_id: datas.order_id,                   //ID
        complain_type: datas.complain_type,         //
        complain_desc: datas.complain_desc,         //
        images: datas.images,                       //
        start_time: datas.start_time,               //
        complain_status: datas.complain_status,     //1 
        is_self: datas.is_self,                     //1 
        complain_consult: datas.complain_consult,   //[]
      })
      // console.log(res.data);
    })
    .catch( error => {
      console.log(error);
    })
  }
  componentWillUnmount = () => {
    this.setState = (state,callback)=>{
      return;
    };
}

  // 
  showDrawer = () => {
    this.setState({
      visible: true
    })
  }
  //  
  onClose = () => {
    this.setState({
      visible: false,
    });
  };
  //  
  onConfirm = () => {
    let dataState = this.state;
      // console.log(dataState.itme_key);
    let this_ = this;
    if ( dataState.itme_key === null ) {
      message.error("")
    } else {
      axios.post(global.constants.website+'/api/task/backoutTaskApply', {
        order_id: dataState.order_id,           //ID
        backout_type: dataState.itme_key,       //
        backout_cause: dataState.itme_key === "1" ? "" : ( dataState.itme_key === "2" ? "" : (dataState.itme_key === "3" ? "" : (dataState.itme_key === "5" ? "" : (dataState.itme_key === "6" ? "" : "" )) ))
      },{
        headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
      })
      .then( res => {
        // console.log(res);
        if ( res.data.status ) {
          this.setState({
            visible: false,
          });
          this_.props.history.push('/myTask');
          message.success(res.data.msg);
        } else {
          message.error(res.data.msg);
        }
      })
      .catch( error => {
        console.log(error);
      })
    }
  }

  // 
  plainTextPrompt = () => {
    prompt('', '', [
      {text: ''},
      {text: '', onPress: value => {
        axios.post(global.constants.website+'/api/task/replyComplain',{
          order_id: localStorage.getItem("order_id"),   //order_id
          consult_desc: value,        //val
        },{
          headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
        })
        .then( res => {
          console.log(res.data);
          if ( res.data.status ) {
            message.success(res.data.msg);
          } else {
            message.error(res.data.msg)
          }
        })
        .catch( error => {
          console.log(error);
        })
      }}
    ])
  }

  // 
  showConfirm = () => {
    let this_ = this;
    alert('', '', [
      {text: '', onPress: () => {console.log('')}},
      {text: '', onPress: () => {
        axios.post(global.constants.website+'/api/task/completeAppeal',{
          order_id: localStorage.getItem("order_id"),   //order_id
        },{
          headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
        })
        .then( res => {
          console.log(res.data);
          if ( res.data.status ) {
            message.success(res.data.msg)
            this_.props.history.push("/myTask");
          } else {
            message.error(res.data.msg)
          }
        })
        .catch( error => {
          console.log(error);
        })
      }}
    ])
  }

  // 
  jieruBtn = () => {
    alert('', '', [
      {text: '', onPress: () => {console.log('')}},
      {text: '', onPress: () => {
        axios.post(global.constants.website+'/api/task/applyToPlatfrom',{
          order_id: localStorage.getItem("order_id"),   //order_id
        },{
          headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
        })
        .then( res => {
          console.log(res.data);
          if ( res.data.status ) {
            message.success(res.data.msg)
          } else {
            message.error(res.data.msg)
          }
        })
        .catch( error => {
          console.log(error);
        })
      }}
    ])
  }

  render() {
    const { is_self,complain_status,complain_consult,start_time,images,complain_desc,complain_type,order_id,goodspic } = this.state;
    return(
      <div>
        <header className="tabTitle">
          <div className="return"><Link to="/dfTaskNo"><Icon type="left" theme="outlined" /></Link></div>
          
        </header>
        <div className="top_">
          <div className="_header">
            <div className="_headerImg">
              <img src={goodspic} alt=""/>
            </div>
            <ul>
              <li>
                <p></p>
                <p>{order_id}</p>
              </li>
              {/* <li>
                <p>QQ</p>
                <p>{seller_qq}</p>
              </li> */}
              <li>
                <p></p>
              </li>
            </ul>
          </div>
          <div className="quxiaoBtn">
            <Button type="primary" onClick={this.showDrawer}></Button>
          </div>
          <p></p>
          <div className="center_detail">
            <img src={require('../../../../img/zhong.png')} alt=""/>
            <p></p>
          </div>
          <div className="shop-titles">
            <span>1</span><span className="shop-titles_span"> {start_time}</span>
            <div className="shensu_phones">
              {
                images ?
                  images.map((item, index) => {
                    return(
                      <img key={index} src={item} alt=""/>
                    )
                  })
                :
                <img src={require("../../../../img/img2.png")} alt=""/>
              }
              <p>{complain_type} {complain_desc}</p>
            </div>
          </div>
          <div className="shop-titles">
            <span style={ complain_consult ? {backgroundColor: "#66caa8"} : {backgroundColor: "#ccc"} }>2</span><span className="shop-titles_span"></span>
            <div className="shensu_phones">
              {
                complain_consult ?
                  complain_consult.map((item, index) => {
                    return(
                      <div key={index} className="xieshang">
                        <p>{item.addtime}</p>
                        <p>{item.type}{item.consult_desc}</p>
                      </div>
                    )
                  })
                :
                ""
              }
            </div>
          </div>
          <div className="shop-titles">
            <span style={ complain_status === 1 ? {backgroundColor: "#66caa8"} : {backgroundColor: "#ccc"} }>3</span><span className="shop-titles_span"></span>
            <div className="shensu_phones">

            </div>
          </div>
          {
            complain_status ?
              ""
            :
            <div className="shensu_btns">
              <Button type="primary" onClick={this.plainTextPrompt}></Button>
              {
                is_self ?
                  <Button type="primary" onClick={this.showConfirm}></Button>
                :
                  ""
              }
              <Button type="primary" onClick={this.jieruBtn}></Button>
            </div>
          }
        </div>


        <Drawer
          title=""
          width={"90%"}
          placement="right"
          onClose={this.onClose}
          maskClosable={false}
          visible={this.state.visible}
          style={{
            height: 'calc(100% - 55px)',
            overflow: 'auto',
            paddingBottom: 53,
            padding:0
          }}>
          <Menu
            defaultOpenKeys={['sub1']}
            mode="inline"
            theme="white"
            inlineCollapsed={this.state.collapsed}
            onClick={item => {this.setState({itme_key: item.key})}}  //key 
          >
            <Menu.Item key="1">
              <Icon type="stop" theme="twoTone" />
              <span></span>
            </Menu.Item>
            <Menu.Item key="2">
              <Icon type="warning" theme="twoTone" />
              <span></span>
            </Menu.Item>
            <Menu.Item key="3">
              <Icon type="money-collect" theme="twoTone" />
              <span></span>
            </Menu.Item>
            <Menu.Item key="5">
              <Icon type="shopping" theme="twoTone" />
              <span></span>
            </Menu.Item>
            <Menu.Item key="6">
              <Icon type="alert" theme="twoTone" />
              <span></span>
            </Menu.Item>
            <Menu.Item key="7">
              <Icon type="api" theme="twoTone" />
              <span></span>
            </Menu.Item>
          </Menu>
          <div
            style={{
              position: 'absolute',
              bottom: 0,
              width: '100%',
              borderTop: '1px solid #e8e8e8',
              padding: '10px 16px',
              textAlign: 'right',
              left: 0,
              background: '#fff',
              borderRadius: '0 0 4px 4px',
            }}>
            <Button style={{ marginRight: 8,}} onClick={this.onClose}></Button>
            <Button onClick={this.onConfirm} type="primary"></Button>
          </div>
        </Drawer>
      </div>
    )
  }
}

export default AppealTaskDetails
