import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Icon, Cascader, Input, Button, message } from 'antd';
import axios from 'axios';
import ImagePicker from 'antd-mobile/lib/image-picker';
import ActivityIndicator from 'antd-mobile/lib/activity-indicator';
import WingBlank from 'antd-mobile/lib/wing-blank';

import './appealTask.css';
import '../../../../component/apis';

const { TextArea } = Input;
const options = [{
    value: 1,
    label: '',
  },{
    value: 2,
    label: '',
  },{
    value: 3,
    label: '',
  },{
    value: 4,
    label: '',
    }];
const data = [];

class AppealTask extends Component {
  constructor(props) {
    super();
    this.state = {
      animating: false,
      files: data,        //
      tousu: null,        //
      contentInput: null,  //
    }
    // console.log(props);
  }

  UNSAFE_componentWillMount() {
    axios.post(global.constants.website+'/api/task/appealTask',{
      order_id: localStorage.getItem("order_id"),   //order_id
    },{
      headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
    })
    .then( res =>{
      let issueType = res.data.data;
      this.setState({
        order_id: issueType.order_id
      })
      // console.log(issueType);
    })
    .catch( error => {
      console.log(error);
    })
  }

  // 
  onChange = (value) => {
    // console.log(value);
    this.setState({
      tousu: value
    })
  }

  // 
  uploadImg = (files, type, index) => {
    // console.log(files);
    this.setState({
      files,
    });
  }
  // 
  contentInput = (e) => {
    this.setState({
      contentInput: e.target.value
    })
    // console.log(e.target.value);
  }

  //  ajax
  uploadShouHuo = () => {
    let this_ = this;
    let _this = this.state;
    let photos = this.state.files;        //
    // imgs 
      let imgs = [];
      if ( photos ) {                   //numtrue  base
        for (var i = 0; i < photos.length; i++) {
          imgs.push(photos[i].url)
        }
      }
    if ( _this.tousu == null || _this.contentInput == null || photos.length > 2 ) {
      if ( photos.length > 2 ) {
        message.error('2');
      } else {
        message.error('');
      }
    }else {
      this_.setState({ animating: true })            //login.....
      // let imgs = [photos[0].url, photos[1].url];    //
      let numType = _this.tousu[0];
      //ajax
      axios.post(global.constants.website+'/api/task/appealTaskCommit',{
        order_id: _this.order_id,
        complain_type: numType,
        complain_desc: _this.contentInput,
        images: imgs,
      },{
        headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
      })
      .then( res => {
        // console.log(res);
        if ( res.data.status ) {
          this_.setState({ animating: false })          //login.....
          message.success(res.data.msg);
          this_.props.history.push("/myTask");
        } else {
          this_.setState({ animating: false })          //login.....
          message.success(res.data.msg);
        }
      })
      .catch( error => {
        console.log(error);
      })
    }

  }

  render() {
    const { files,animating } = this.state;
    return(
      <div>
        <header className="tabTitle">
          <div className="return"><Link to="/myTaskDetails"><Icon type="left" theme="outlined" /></Link></div>
          
        </header>
        <div>
          <WingBlank>
            <div className="shensu-box">
              <p className="_p" style={{ marginBottom: '0' }}></p>
            </div>
            <div className="shensu-box-child">
              <p className="_p" style={{ marginBottom: '1rem' }}></p>
              <Cascader style={{ width:'100%' }} options={options} onChange={this.onChange} placeholder="" />
              <p className="_p __p"></p>
              <TextArea style={{ width:'100%' }} rows={4} onChange={ this.contentInput } placeholder="" />
              <p className="_p"></p>
              <ImagePicker
                length={2}
                files={files}
                multiple={false}
                onChange={this.uploadImg}
                onImageClick={(index, fs) => console.log(index, fs)}
                selectable={files.length < 2}
              />
            </div>
            <Button type="primary" className="login-form-button" onClick={this.uploadShouHuo}>
              
            </Button>
            <div className="toast-example">
              <ActivityIndicator
                toast
                text="..."
                animating={animating}
              />
            </div>
          </WingBlank>
        </div>
      </div>
    )
  }
}

export default AppealTask
