import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { Icon, Form, Input, Button, message } from 'antd';
import ActivityIndicator from 'antd-mobile/lib/activity-indicator';
import WingBlank from 'antd-mobile/lib/wing-blank';
import '../../../../component/apis';

const FormItem = Form.Item;
const phoneNum = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/;   //

class Correct_Accounts extends Component {
  constructor(props) {
    super();
    this.state = {
      animating: false,
      placeholder: "",
      code: null,
      getCodesState: true,
      codeNum: 60,        // 60 
      TestGetCode: "",
    }
    // console.log(props);
  }

  UNSAFE_componentWillMount() {
    //  
    axios.get(global.constants.website+'/api/user/getcaptcha')
    .then(response => {
      // console.log(response.data.data);
      this.setState({
        tuCodeLink: response.data.data.captcha_src,
        sid: response.data.data.sid,
      })
      // console.log(response.data.data);
    })
    .catch(error => {
      console.log(error);
    })
  }
  componentWillUnmount = () => {
    this.setState = (state,callback)=>{
      return;
    };
  }

  //  
  getVerifyCode = () => {
    axios.get(global.constants.website+'/api/user/getcaptcha')
    .then(response => {
      this.setState({
        tuCodeLink: response.data.data.captcha_src,
        sid: response.data.data.sid,
      })
    })
    .catch(error => {
      console.log(error);
    })
    // console.log(this.state.tuCode);
  }
  tuCodes = (e) => {
    this.setState({
      code: e.target.value
    })
  }

  //  onChange placeholder 
  numbersPlace = (e) => {
    this.setState({
      placeholder: e.target.value
    })
  }

  // 
  duanXinCodeBtn = () => {
    let dataCode_ = this.state;
    let this_ = this;
    if ( dataCode_.placeholder === "" || !phoneNum.test(dataCode_.placeholder) ) {
      message.error("");
    }else if (dataCode_.code === null) {
      message.error("");
    } else  {
      // 
      axios.post(global.constants.website+'/api/user/sendcode',
      {
        phoneNum: dataCode_.placeholder,
        tuCode: dataCode_.code,
        sid: dataCode_.sid,
      },
      {
        headers: {AppAuthorization: localStorage.getItem("token")}        //post  token
      })
      .then( response=> {
        // console.log(response);
        if ( response.data.status ) {
          //  60
          let codeNum = dataCode_.codeNum;
          const timer = setInterval(() => {
          this_.setState({
            getCodesState:false,
            phoneCode: response.data.code,
            codeNum: (codeNum--)
            }, () => {
                if (codeNum === 0) {
                clearInterval(timer);
                this_.setState({
                  getCodesState: true,
                  codeNum: 60,
                  TestGetCode: ""
                })
              }
            })
          }, 1000);
          message.success(response.data.msg);
        } else {
          message.error(response.data.msg);
        }
      })
      .catch( error=> {
        console.log(error);
      });
    }
  }

  // ajax
  nextHandleSubmit = (e) => {
    e.preventDefault();
    let Submitthis = this;
    let scode = Submitthis.props.location.state.scode;
    this.props.form.validateFields((err, values) => {
      if (!err) {
        Submitthis.setState({ animating: true });            //login.....
        axios.post(global.constants.website+'/api/index/changemobileTwo', {
          scode: scode,
          mobile: values.numb,
          password: values.loginPassword,
          smscode: values.dtCode,
        },
        {
          headers: {AppAuthorization: localStorage.getItem("token")}        //post  token
        })
        .then( response => {
          // console.log(response.data);
          if ( response.data.status ) {
            Submitthis.setState({ animating: false })          //login.....
            message.success(response.data.msg);
              this.props.history.push('/personal')      //
          } else {
            Submitthis.setState({ animating: false })          //login.....
            message.error(response.data.msg);
          }
        })
        .catch( error => {
          console.log(error);
        })
      }
    });
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    const { animating, tuCodeLink, TestGetCode, getCodesState, codeNum } = this.state;
    return(
      <div>
        <header className="tabTitle">
          <div className="return"><Link to="/personal"><Icon type="left" theme="outlined" /></Link></div>
          
        </header>
        <WingBlank>
          <Form onSubmit={this.handleSubmit} className="login-form" style={{ padding: '3.5rem 0.7rem 0 0.7rem' }}>
            <FormItem
              label=""
            >
              {getFieldDecorator('numb', {
                rules: [{ required: true, message: '!' }],
              })(
                <Input className="buy-input"  onChange={ this.numbersPlace } maxLength={11} placeholder={ this.state.placeholder } />
              )}
            </FormItem>
            <FormItem
              label=""
            >
              <div style={{ display: 'flex' }}>
                <img style={{ width: "80%" }} src={ tuCodeLink } alt=""/>
                <Button style={{ height: 'auto' }} onClick={ this.getVerifyCode }></Button>
              </div>
              {getFieldDecorator('code', {
                rules: [{ required: true, message: '!' }],
              })(
                <Input className="buy-input" onChange={ this.tuCodes } type="text" placeholder="" />
              )}
            </FormItem>
            <FormItem
              label=""
            >
              {getFieldDecorator('dtCode', {
                rules: [{ required: true, message: '!' }],
              })(
                <Input className="buy-input" type="Number" placeholder="" />
              )}
            </FormItem>
            <FormItem
              label=""
            >
              {getFieldDecorator('loginPassword', {
                rules: [{ required: true, message: '!' }],
              })(
                <Input className="buy-input" type="password" placeholder="" />
              )}
            </FormItem>
            {
              getCodesState
                ? <Button type="primary" onClick={this.duanXinCodeBtn} className="accountBtn">{ TestGetCode }</Button>
                : <Button className="accountBtn" disabled="disabled">{codeNum}</Button>
            }
            {/* <Button type="primary" onClick={this.duanXinCodeBtn} className="accountBtn">{ TestGetCode }</Button> */}
            <Button type="primary" htmlType="submit" className="accountBtn" onClick={ this.nextHandleSubmit }></Button>
            <div className="toast-example">
              <ActivityIndicator
                toast
                text="..."
                animating={animating}
              />
            </div>
          </Form>
        </WingBlank>
      </div>
    )
  }
}

const Correct_Account = Form.create()(Correct_Accounts);
export default Correct_Account
