import React, { Component } from 'react';
import { Progress, Modal, message  } from 'antd';
import { Link } from 'react-router-dom';

class TaskPlan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      previewVisibleOne: false,
      previewVisibleTwo: false,
    }
    // console.log(props);
  }

  // 
  showsShili = (item) => {
    this.setState({
      previewVisibleOne: true,
      photosLink: item,
    });
    // console.log(item);
  }
  showsShilia = () => {
    this.setState({
      previewVisibleTwo: true,
      photosLinks: this.props.addition_pic,
    });
  }
  //
  handleCancel = () => {
    this.setState({
      previewVisibleOne: false,
      previewVisibleTwo: false,
    })
  }

  // 
  reminder = () => {
    message.success("48")
  }

  render() {
    const { photosLink,photosLinks,previewVisibleOne,previewVisibleTwo } = this.state;
    const { fav_flow_content,task_type,platformname,addition_pic,is_addcomments,receive_evaluate_content,taobao_ordersn, addtime, need_principal, order_id, user_taobao, itemprice, itemnum, chat_pay_content, order_status } = this.props;
    return(
      <div className="task-plan">
        {
          task_type === "1" ?
            <div>
              {/* <div className="plan">
                <Progress className="plan-child" type="circle" percent={ order_status === 0? 25 : (order_status === 1? 50 : (order_status ===3? 75 : (order_status ===5? 95 : 100) )) } width={80} />
                {
                  order_status === 0 ?
                <p></p>
                  :
                  ( order_status === 1 ?
                <p>48</p>
                  :
                  ( order_status === 3 ?
                <p></p>
                  :
                  <p></p>
                  )
                  )
                }
              </div> */}
              <div className="plan-box">
                <p className="title"><b className="plan-title"></b><span>{addtime}</span></p>
                <p className="task-plan-list"><span></span><span>{order_id}</span></p>
                <p className="task-plan-list"><span></span><span>{user_taobao}</span></p>
                <div className="task-plan-list"><span></span><span>{itemprice}*{itemnum}</span></div>
              </div>
              {/*  */}
              <div className="plan-box">
                <p className="title"><b className="plan-title"></b><span></span></p>
                <div className="task-plan-list"><span></span>
                  <div className="task-plan-list-img">
                    {/*  */}
                    {
                      fav_flow_content ?
                        fav_flow_content.map((item, index) => {
                          return(
                            <img key={index} onClick={()=>this.showsShili(item)} src={ item } alt="" />
                          )
                        })
                      :
                      ""
                    }
                    {/*    */}
                    <Modal visible={previewVisibleOne} footer={null} onCancel={this.handleCancel}>
                      <img alt="example" style={{ width: '100%' }} src={photosLink} />
                    </Modal>
                    <Modal visible={previewVisibleTwo} footer={null} onCancel={this.handleCancel}>
                      <img alt="example" style={{ width: '100%' }} src={photosLinks} />
                    </Modal>
                  </div>
                </div>
              </div>
              {/*  */}
              <div className="plan-box">
                <p className="title"><b className="plan-title"></b><span></span></p>
                <div className="task-plan-list">
                  <span className="ask-start">48</span>
                  <button style={ order_status === 4 ? {backgroundColor: '#66caa8'} : {backgroundColor: '#ccc'} }></button>
                </div>
              </div>
            </div>
          :
          // 
          <div>
            <div className="plan">
              <Progress className="plan-child" type="circle" percent={ order_status === 0? 25 : (order_status === 1? 50 : (order_status ===3? 75 : (order_status ===5? 95 : 100) )) } width={80} />
              {
                order_status === 0 ?
                  <p></p>
                :
                ( order_status === 1 ?
                  <p>48</p>
                :
                ( order_status === 3 ?
                  <p></p>
                :
                ( order_status === 5 ?
                  <p></p>
                :
                <p></p>
                )
                )
                )
              }
            </div>
            <div className="plan-box">
              <p className="title"><b className="plan-title"></b><span>{addtime}</span></p>
              <p className="task-plan-list"><span></span><span>{order_id}</span></p>
              <p className="task-plan-list"><span></span><span>{user_taobao}</span></p>
              <div className="task-plan-list"><span></span><span>{itemprice}*{itemnum}</span></div>
            </div>
            {/*  */}
            <div className="plan-box">
              <p className="title"><b className="plan-title"></b><span></span></p>
              <p className="task-plan-list"><span></span><span></span></p>
              <p className="task-plan-list"><span></span><span></span></p>
              <div className="task-plan-list"><span></span>
                <div className="task-plan-list-img">
                  {/* {
                    numbers === 1?
                    <img onClick={this.handlePreviewOne} src={ chat_pay_content ? chat_pay_content[0] : require('../../../img/img2.png') } alt="" />
                    : (numbers === 2?
                    <img onClick={this.handlePreviewOne} src={ chat_pay_content ? chat_pay_content[0] : require('../../../img/img2.png') } alt="" />
                    <img onClick={this.handlePreviewTwo} src={ chat_pay_content ? chat_pay_content[1] : require('../../../img/img2.png') } alt="" />
                    : (numbers === 3?
                    <img onClick={this.handlePreviewOne} src={ chat_pay_content ? chat_pay_content[0] : require('../../../img/img2.png') } alt="" />
                    <img onClick={this.handlePreviewTwo} src={ chat_pay_content ? chat_pay_content[1] : require('../../../img/img2.png') } alt="" />
                    <img onClick={this.previewVisibleTwos} src={ chat_pay_content ? chat_pay_content[2] : require('../../../img/img2.png') } alt="" />
                    : 'd'))
                  } */}
                  {/*  */}
                  {
                    chat_pay_content ?
                      chat_pay_content.map((item, index) => {
                        return(
                          <img key={index} onClick={()=>this.showsShili(item)} src={ item } alt="" />
                        )
                      })
                    :
                    ""
                  }
                  {/*    */}
                  <Modal visible={previewVisibleOne} footer={null} onCancel={this.handleCancel}>
                    <img alt="example" style={{ width: '100%' }} src={photosLink} />
                  </Modal>
                  <Modal visible={previewVisibleTwo} footer={null} onCancel={this.handleCancel}>
                    <img alt="example" style={{ width: '100%' }} src={photosLinks} />
                  </Modal>
                </div>
              </div>
              <div className="task-plan-list">
                <span className="ask-start"></span>
                <button style={ order_status === 0 ? {backgroundColor: '#66caa8'} : {backgroundColor: '#ccc'} }></button>
              </div>
            </div>
            {/*  */}
            <div className="plan-box">
              <p className="title"><b className="plan-title"></b></p>
              <p className="task-plan-list"><span>{platformname}</span><span>{taobao_ordersn}</span></p>
              <p className="task-plan-list"><span></span><span></span></p>
              <p className="task-plan-list"><span></span><span></span></p>
              <p className="task-plan-list"><span></span><span>{need_principal}</span></p>
              <div className="task-plan-list">
                <span className="ask-start">48</span>
                <button onClick={ order_status === 1 ? this.reminder : this.aa } style={ order_status === 1 ? {backgroundColor: '#66caa8'} : {backgroundColor: '#ccc'} }></button>
              </div>
            </div>
            {/*  */}
            <div className="plan-box">
              <p className="title"><b className="plan-title"></b><span></span></p>
              <div className="task-plan-list"><span></span>
                <div className="task-plan-list-img">
                  {/*  */}
                  {
                    receive_evaluate_content ?
                      receive_evaluate_content.map((item, index) => {
                        return(
                          <img key={index} onClick={()=>this.showsShili(item)} src={ item } alt="" />
                        )
                      })
                    :
                    ""
                  }
                </div>
              </div>
              <div className="task-plan-list">
                <span className="ask-start"> </span>
                {
                  order_status === 3 ?
                    <button style={{backgroundColor: '#66caa8'}}><Link style={{ color: '#fff' }} to={{ pathname: "/goodPingJia", state: order_id }}></Link></button>
                  :
                  <button style={{backgroundColor: '#ccc'}}></button>
                }
              </div>
            </div>
            {/*  */}
            {
              is_addcomments ?
                <div className="plan-box">
                  <p className="title"><b className="plan-title">{ is_addcomments? "" : "" }</b><span></span></p>
                  <div className="task-plan-list"><span></span>
                    <div className="task-plan-list-img">
                      {/*  */}
                      {
                        addition_pic ?
                          <img onClick={this.showsShilia} src={ addition_pic } alt="" />
                        :
                        ""
                      }
                    </div>
                  </div>
                  <div className="task-plan-list">
                    <span className="ask-start"> </span>
                    {
                      order_status === 5 ?
                        <button style={{backgroundColor: '#66caa8'}}><Link style={{ color: '#fff' }} to={{ pathname: "/addPingJia", state: order_id }}></Link></button>
                      :
                      <button style={{backgroundColor: '#ccc'}}></button>
                    }
                  </div>
                </div>
              :
              ""
            }
            {/*  */}
            <div className="plan-box">
              <p className="title"><b className="plan-title">{ is_addcomments? "" : "" }</b><span></span></p>
              <div className="task-plan-list">
                <span className="ask-start">48</span>
                <button style={ order_status === 4 ? {backgroundColor: '#66caa8'} : {backgroundColor: '#ccc'} }></button>
              </div>
            </div>
          </div>
        }
      </div>
    )
  }
}

export default TaskPlan
