import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Icon, Button, Input, message } from 'antd';
import axios from 'axios';
import ActivityIndicator from 'antd-mobile/lib/activity-indicator';
import WingBlank from 'antd-mobile/lib/wing-blank';

import './questionsTask.css';
import '../../../component/apis';

class QuestionsTasks extends Component {
  constructor(props) {
    super(props);
    this.state= {
      animating: false,
      inputValue: null,         //
    }
    // console.log(props.location.state);
  }

  UNSAFE_componentWillMount() {
    let this_ = this;
    axios.post(global.constants.website+'/api/task/askTaskTwo',
    {
      order_id: this_.props.location.state,
    },
    {
      headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
    })
    .then(function (response) {
      let data_ = response.data.data;
      console.log(data_);
      this_.setState({
        id: data_.order_id,                                   //ID
        goodsname: data_.goodsname,                     //
        goodspic: data_.goodspic,                       //
        questype: data_.questype,                       //
        quescontent: data_.quescontent,                 //
        question_content: data_.question_content,       //
        answer_pic_content: data_.answer_pic_content,   //
        check_question: data_.check_question,           //
      })
    })
    .catch(function (error) {
      console.log(error);
    });
  }

  //
  onInputValue = (e) => {
    this.setState({
      inputValue: e.target.value
    })
  }

  // 
  submitBtn = () => {
    let _this = this.state;
    let this_ = this;
    console.log(_this.id);
    if ( _this.inputValue === null ) {
      message.error("")
    } else {
      this_.setState({ animating: true })            //login.....
      axios.post(global.constants.website+'/api/task/sublink',{
        order_id: _this.id,                       //ID
        answer_link: _this.inputValue,                 //
      },{
        headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
      })
      .then( res => {
        console.log(res.data);
        if ( res.data.status ) {
          this_.setState({ animating: false })          //login.....
          this_.props.history.push("/wenDaTaskNo");
          message.success(res.data.msg);
        } else {
          this_.setState({ animating: false })          //login.....
          message.error(res.data.msg);
        }
      })
      .catch( err => {
        console.log(err);
      })
    }

  }


  render() {
    const radioStyle = {
      display: 'block',
      height: '30px',
      lineHeight: '30px',
    };
    const { check_question,answer_pic_content,question_content,goodsname,goodspic,quescontent,questype,animating } = this.state;
    return(
      <div>
        <header className="tabTitle">
          <div className="return"><Link to="/taskHallPage"><Icon type="left" theme="outlined" /></Link></div>
          
        </header>
        <div>
          <WingBlank style={{ margin: '0' }}>
            <div className="questionsTask-box">
              <div className="product-details">
                <div style={{ width: '30%' }}>
                  <img style={{ maxWidth: '100%',paddingRight: '0.5rem' }} src={goodspic} alt=""/>
                </div>
                <p style={{ width: '70%' }}>{goodsname}</p>
              </div>
              <div className="taskList">
                {
                  questype === 1 ?
                    <div>
                      <p className="centerTitle"></p>
                      <p className="zhongs">{check_question}</p>
                      <div style={{ width: '100%', wordBreak: 'keep-all', overflow: 'auto',marginLeft: '0.9rem' }}>
                        {
                          quescontent.map((item, index) => {
                            return(
                              <p key={index} style={radioStyle} value={item}>{item}</p>
                            )})
                        }
                      </div>
                    </div>
                  :
                  <div>
                    <p className="centerTitle"></p>
                    <p style={{ padding: '0.5rem 0', fontSize: '1rem' }}>{question_content}</p>
                  </div>
                }
              </div>
              <p className="fontTu"></p>
              <img className="photos_a" src={answer_pic_content} alt=""/>
              <p className="fontTu">!</p>
              <Input onChange={ this.onInputValue } style={{ margin: '0 0 1rem 0', width: '100%' }} placeholder="" />
            </div>
            <Button onClick={ this.submitBtn } className="btn-buy" type="primary"></Button>
            <div className="toast-example">
              <ActivityIndicator
                toast
                text="..."
                animating={animating}
              />
            </div>
          </WingBlank>
        </div>
      </div>
    )
  }
}
export default QuestionsTasks
