import React, { Component } from 'react';
import { Modal, Input, Button, Form, message  } from 'antd';
import ImagePicker from 'antd-mobile/lib/image-picker';
import axios from 'axios';
import ActivityIndicator from 'antd-mobile/lib/activity-indicator';
import WingBlank from 'antd-mobile/lib/wing-blank';
// import '../../../../../component/apis';

// import { pageInputScroll } from '../../../../../component/pageInputScroll';
// import TaskStateUpload from '../taskStateUpload/taskStateUpload';

const FormItem = Form.Item;
const data = [];
let taobaoOride =  /^T200P\d{18}$/;   //
let jingdongOride = /^\d{11,12}$/;       //
let pinduoduoOride = /^\d{6}[-]\d+$/; //
let weipinhuiOride = /^\d{14}$/;      //

message.config({
  top: 300,
});

class LookShiliTus extends Component {
  constructor(props) {
    super(props);
    this.state = {
      animating: false,
      onevisible: false,
      onevisibles: false,
      twovisible: false,
      threevisible: false,
      files: data,
      oks: false,                                                          //
      oks2: this.props.is_staytime ? false : true,                         //
    }
    // console.log(props);
  }

  // componentDidMount(){
  //      pageInputScroll()
  //  }

  // 
  showOneShiliTu = () => {
    this.setState({
      onevisible: true,
    })
  }
  showOneShiliTus = () => {
    this.setState({
      onevisibles: true,
    })
  }
  // 
  showTwoShiliTu = () => {
    this.setState({
      twovisible: true
    })
  }
  // 
  showThreeShiliTu = () => {
    this.setState({
      threevisible: true
    })
  }

  // 
  heDuiName = () =>  {
    let inshop_name = this.state.inshop_name;
    let getshop_name = this.props.shop_name;
    // console.log("");
    if ( inshop_name === getshop_name ) {
      message.success("")
      this.setState({
        oks: true,
      })
    } else {
      message.error("")
    }
  }
  // value
  shopName =(e) => {
    this.setState({
      inshop_name: e.target.value
    })
    // console.log(e.target.value);
  }

  // value
  shopAnswer =(e) => {
    this.setState({
      answer: e.target.value
    })
    // console.log(e.target.value);
  }
  answerBtn = () => {
    axios.post(global.constants.website+'/api/task/checkQuestion', {
      stayanswer: this.state.answer,                           //
      task_id: this.props.task_id,                             //ID
    },{
      headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
    })
    .then( res => {
      if ( res.data.status ) {
        message.success(res.data.msg)
        this.setState({
          oks2: true,
        })
      } else {
        message.error(res.data.msg)
      }
    })
    .catch(error => {
      console.log(error);
    })
  }

  // 
  handleOk = (e) => {
    // console.log(e);
    this.setState({
      onevisible: false,
      onevisibles: false,
      twovisible: false,
      threevisible: false
    });
  }
  handleCancel = (e) => {
    // console.log(e);
    this.setState({
      onevisible: false,
      onevisibles: false,
      twovisible: false,
      threevisible: false
    });
  }

  //  
  onChange = (files, type, index) => {
    // console.log(files);
    this.setState({
      files,
    });
  }

  // niantieBtn = (e) => {
  //   var target = this;
  //   console.log(target);
  // }

  //  
  handleSubmit = (e) => {
    e.preventDefault();
    let this_ = this;
    let photos = this.state.files;        //
    let order_id = this.props.order_id;
    let imgs = [];    //
    for (var i = 0; i < photos.length; i++) {
      imgs.push(photos[i].url)
    }
    // console.log(this.props.pic_uploads_num);
    // console.log(imgs);
    this.props.form.validateFields((err, values) => {
      if ( this.state.oks === false ) {
        message.error("")
      } else if ( this.state.oks2 === false ) {
        message.error("")
      } else {
        if ( !err === true && imgs.length === this.props.pic_uploads_num ) {
          if ( !taobaoOride.test(values.orderNumber) && !jingdongOride.test(values.orderNumber) && !pinduoduoOride.test(values.orderNumber) && !weipinhuiOride.test(values.orderNumber) ) {
            message.error("")
          } else {
            this_.setState({ animating: true })            //login.....
            // ajax
            axios.post(global.constants.website+'/api/task/operateTaskCommit', {
              taobao_ordersn: values.orderNumber,   //
              order_id: order_id,                   //ID
              need_principal: values.money,         //
              chat_pay_content: imgs,               //
            },{
              headers: {AppAuthorization: localStorage.getItem("token")}    //post  token
            })
            .then( res => {
              // console.log(res.data.data);
              if ( res.data.status ) {
                this_.setState({ animating: false })          //login.....
                this_.props.history.push("/myTaskDetails");
                message.success(res.data.msg);
              } else {
                this_.setState({ animating: false })          //login.....
                message.error(res.data.msg)
              }
            })
            .catch(error => {
              console.log(error);
            })
          }
        }else {
          if ( imgs.length > this.props.pic_uploads_num ) {
            message.error(''+ this.props.pic_uploads_num + '');
          } else {
            message.error('');
          }
        }
      }
    });
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    const { files, animating } = this.state;
    const { is_staytime,stay_pic, stayquestion, itemnum, itemprice,chatpic,is_muti_keyword,shop_namess, pic_uploads_num, pic_desc, platform,tasktype_itemname,platformname,user_taobao } = this.props;
    return(
      <div>
        {/*  */}
        <div className="task-plan buzhou" style={{ marginBottom: '0' }}>
          <WingBlank>
            <div className="buzou-title"><span> </span><span onClick={this.showOneShiliTu}></span></div>
            <p>. <span style={{ color: 'red' }}>{user_taobao}</span>{platformname}{tasktype_itemname}APP</p>
            <p>.{tasktype_itemname}</p>
            <p>.2-41-3</p>
            <p>.:{platformname}APP{platformname}APP</p>
            <div style={{ display: 'flex', alignItems:'center',justifyContent:'space-between' }}>
              <h3 style={{ color:'#c15958', marginTop:'1rem' }}></h3>
              {
                stay_pic === "" ?
                  ''
                :
                <p onClick={this.showOneShiliTus} style={{ marginTop: '1rem',fontSize: '1rem',color:'red' }}></p>
              }
            </div>
            <div className="shop-title">
              <span>1</span><span>:{shop_namess}</span>
            </div>
            <div className="shop-title">
              <span>2</span>
              <Input onChange={ this.shopName } placeholder="" />
              <Button type="primary" onClick={ this.heDuiName }></Button>
            </div>
            {
              is_staytime ?
                <div>
                  <div className="shop-title">
                    <span>3</span><span>:{stayquestion}</span>
                  </div>
                  <div className="shop-title">
                    <span>4</span>
                    <Input onChange={ this.shopAnswer } placeholder="" />
                    <Button type="primary" onClick={ this.answerBtn }></Button>
                  </div>
                </div>
              :
              ""
            }
            {/*   */}
            <div className="buzou-title"><span> </span><span onClick={this.showTwoShiliTu}></span></div>

            {
              platform === 5 ?
                <div>
                  <p>.2-3</p>
                  <p></p>
                </div>
              :
              platform === 6 ?
                <div>
                  <p>.2-3</p>
                  <p>.1</p>
                  <p>.</p>
                </div>
              :
              platform === 2 ?
                <div>
                  <p>.2-3</p>
                  <p>.1</p>
                  <p>.)</p>
                </div>
              :
              platform === 7 ?
                <div>
                  <p>.2-3</p>
                  <p>.1</p>
                </div>
              :
              <div>
                <p>.2-3</p>
                <p>.1</p>
                <p>.:</p>
              </div>
            }
            {/*   */}
            {
              platform === 1 ?
                <div>
                  <div className="buzou-title"><span> {platform === 1 ? "" : ""}</span><span onClick={this.showThreeShiliTu}></span></div>
                  {is_muti_keyword ?
                    <div>
                      <p>.  2-3   </p>
                      <p>.  </p>
                    </div>
                  :
                    ""
                  }
                  <p>.</p>
                  <p>.APPAPP</p>
                  {/* <p style={{ color:'red', fontWeight:'bold' }}>.</p> */}
                  {
                    is_muti_keyword ?
                      ""
                    :
                    (chatpic ?
                      <p className="liaotian"></p>
                    :
                    <p className="liaotian"></p>)
                  }
                </div>
              :
              (platform === 2 ?
                <div className="buzou-title"><span> </span><span onClick={this.showThreeShiliTu}></span></div>
              :
              (platform === 5 ? <div className="buzou-title"><span> </span><span onClick={this.showThreeShiliTu}></span></div>
              : <div className="buzou-title"><span> </span><span onClick={this.showThreeShiliTu}></span></div> )
              )
            }
            {/*   */}
            <ImagePicker
              length={pic_uploads_num}
              files={files}
              multiple={false}
              onChange={this.onChange}
              onImageClick={(index, fs) => console.log(index, fs)}
              selectable={files.length < pic_uploads_num}
            />
            <p className="jietuFont"><span style={{ fontWeight:'bold',fontSize:'1rem',color:'red' }}>{pic_desc}</span></p>
            <div className="buzou-title"><span style={{ color:'#63bb95' }}> </span></div>
            <p>:{itemprice*itemnum}(50)</p>
            {
              platform === 1 ?
                <p style={{ color: 'red', fontWeight:'bold', marginBottom:'1rem' }}></p>
              :
              <p style={{ color: 'red', fontWeight:'bold', marginBottom:'1rem' }}></p>
            }
            <Form className="login-form" layout="inline" onSubmit={this.handleSubmit}>
              <FormItem>
                {getFieldDecorator('money', {
                  rules: [{ required: true, message: '!' }],
                })(
                  <Input type="Number" placeholder="" className="jineInput" />
                )}
              </FormItem>
              {
                platformname === "" ?
                  <FormItem>
                    {getFieldDecorator('orderNumber', {
                      rules: [{ required: true, message: '!' }],
                    })(
                      <Input placeholder="" className="jineInput animated-router-forward-enter-done" />
                      // <div style={{ display: 'flex',}}>
                      //   <Input placeholder="" className="jineInput" />
                      //   <Button onClick={ this.niantieBtn } style={{ marginTop: 0 }}></Button>
                      // </div>
                    )}
                  </FormItem>
                :
                <FormItem>
                  {getFieldDecorator('orderNumber', {
                    rules: [{ required: true, message: '!' }],
                  })(
                    <Input placeholder="" className="jineInput" />
                  )}
                </FormItem>
              }
              <FormItem>
                <Button type="primary" htmlType="submit" className="login-form-button">
                  
                </Button>
              </FormItem>
            </Form>
            <div className="toast-example">
              <ActivityIndicator
                toast
                text="..."
                animating={animating}
              />
            </div>
          </WingBlank>
        </div>


        {/*  */}
        <Modal
          visible={this.state.onevisible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          maskClosable={true}
          okText={""}
          cancelText={""}
        >
          <img className="shilitu" src={require('../../../../../img/4444.jpg')} alt="" />
        </Modal>
        <Modal
          visible={this.state.onevisibles}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          maskClosable={true}
          okText={""}
          cancelText={""}
        >
          <img className="shilitu" src={stay_pic} alt="" />
        </Modal>
        {/*  */}
        <Modal
          visible={this.state.twovisible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          maskClosable={true}
          okText={""}
          cancelText={""}
        >
          <img className="shilitu" src={require('../../../../../img/5555.jpg')} alt="" />
        </Modal>
        {/*  */}
        <Modal
          visible={this.state.threevisible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          maskClosable={true}
          okText={""}
          cancelText={""}
        >
          <img className="shilitu" src={platform === 1? require('../../../../../img/6666.jpg') : (platform === 2? require('../../../../../img/jdshili.jpg') : (platform === 5? require('../../../../../img/pddorder.png') : require('../../../../../img/wphorder.jpg')))} alt="" />
        </Modal>
      </div>
    )
  }
}

const LookShiliTu = Form.create()(LookShiliTus);
export default LookShiliTu
